@import url("../assets/font/Mulish/stylesheet.css");

* {
    box-sizing: border-box;
}

html {
    font-size: 10px;
    scroll-behavior: smooth;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }
}

body {
    font-family: "Mulish";
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
    -webkit-tap-highlight-color: transparent;
    color: $color-text;
    background-color: #f5f5f5;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }

    @media screen and (max-width: 800px) {
        font-size: 14px;
    }

    // @media screen and (max-width: 460px) {
    //     font-size: 14px;
    //   }
    &.actived {
        overflow: hidden;
    }
}

a {
    text-decoration: none;
    color: inherit;

    @include transition(0.3s);

    @include min(1200px) {
        &:hover {
            color: $color-pri;
        }
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active,
input:-webkit-autofill:focus {
    background-color: transparent !important;
    color: black !important;
    -webkit-box-shadow: 0 0 0 1000px transparent inset !important;
    -webkit-text-fill-color: black !important;
}

button,
select {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    cursor: pointer;
}

button {
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

input,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

button,
select,
input,
textarea {
    border: none;
    outline: none;
    box-shadow: none;
    font-family: inherit;
    color: black;
    background: transparent;
    font-family: "Mulish";
    &:focus {
        border: none;
        outline: none;
        box-shadow: none;
    }

    &::placeholder {
        font-family: "Mulish";
    }

    &:focus-visible {
        border: none;
        outline: none;
        box-shadow: none;
    }
}

::-webkit-scrollbar {
    width: 0.2rem;
}

::-webkit-scrollbar-thumb {
    background: $color-pri;
}

::-webkit-scrollbar-track {
    background: $color-white !important;
    border-radius: 0.4rem !important;
}

@supports (-webkit-touch-action: none) {
    input,
    select,
    textarea {
        font-size: max(16px);
    }
}

.main {
    // min-height: 100rem;
    position: relative;
    // overflow: hidden;
    &.pt {
        padding-top: var(--size-hd);
        transition: 0.5s ease;
    }
}

//light gallery

.lg-backdrop {
    z-index: 3000;
}

.lg-outer {
    z-index: 3000;
}

.container {
    width: 100%;
    max-width: 124.5rem;
    height: 100%;
    margin: 0 auto;
    padding: 0 1.5rem;

    // @include max(800px) {
    //     padding: 0 0.8rem;
    // }

    // @include max(600px) {
    //     padding: 0 0.6rem;
    // }
}

.no-scroll {
    touch-action: none;
    -webkit-overflow-scrolling: none;
    overflow: hidden;
    overscroll-behavior: none;
}

iframe {
    vertical-align: middle;
}

img {
    max-width: 100%;
    height: auto;
}

.t-center {
    text-align: center;
}

.fw-b {
    font-weight: bold;
}

.fsi {
    font-style: italic;
}

.t-up {
    text-transform: uppercase;
}

.tabBtn {
    cursor: pointer;
}

.gItem {
    cursor: pointer;
}

.global-title {
    max-width: 0;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
}

.fancybox__container {
    z-index: 5000;
}

.title {
    &-lg {
        @include fz-46;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-md {
        @include fz-40;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }

        strong {
            color: $color-pri;
        }
    }

    &-sm {
        @include fz-32;
    }

    &-sm2 {
        @include fz-36;
    }

    &-sm3 {
        @include fz-40;
        line-height: 1.3;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-sm4 {
        @include fz-48;
    }

    &-mn {
        @include fz-24;
    }

    &-mn2 {
        @include fz-28;
    }
}

.note {
    &-link {
        @include fz-16;
        font-weight: 700;
        color: $color-pri;
        padding-right: 2rem;
        width: fit-content;
        position: relative;

        @media screen and (max-width: 460px) {
            padding-right: 1.6rem;
        }

        &::before {
            content: "";
            background-image: url(../assets/images/Arrow.svg);
            width: 1.4rem;
            height: 1.4rem;
            position: absolute;
            top: 55%;
            right: 0%;
            transform: translateY(-50%);
            background-size: 100% 100%;
            background-repeat: no-repeat;
            @media screen and (max-width: 460px) {
                width: 1.2rem;
                height: 1.2rem;
                top: 48%;
            }
        }
    }

    &-title {
        font-weight: 700;
        @include fz-16;
    }

    &-text {
        @include fz-16;
        strong {
            font-weight: 700;
        }

        @media screen and (max-width: 460px) {
        }
    }

    &-lg {
        @include fz-20;
        font-weight: 600;

        strong {
            color: $color-pri;
        }
    }

    &-md {
        @include fz-18;
    }

    &-sm {
        @include fz-14;
    }

    &-smn {
        @include fz-12;
    }

    &-ic {
        width: 1.2rem;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        i {
            color: $color-pri;
            font-size: 1.4rem;
        }
    }
}

.cl {
    &-pri {
        color: $color-pri;
    }

    &-white {
        color: $color-white;
    }

    &-second {
        color: $color-second;
    }

    &-third {
        color: $color-third;
    }

    &-text {
        color: $color-text;
    }

    &-text2 {
        color: $color-text2;
    }

    &-text3 {
        color: $color-text3;
    }
    &-text4 {
        color: $color-text4;
    }
    &-text5 {
        color: $color-text5;
    }

    &-gray {
        color: $color-gray;
    }

    &-gray2 {
        color: $color-gray2;
    }

    &-gray3 {
        color: $color-gray3;
    }

    &-gray4 {
        color: $color-gray4;
    }
    &-gray5 {
        color: $color-gray5;
    }
    &-gray6 {
        color: $color-gray6;
    }

    &-gray11 {
        color: $color-gray11;
    }

    &-black2 {
        color: $color-black-2;
    }

    &-black3 {
        color: $color-black-3;
    }

    &-black {
        color: $color-black;
    }

    &-red {
        color: $color-red;
    }

    &-blue {
        color: $color-blue;
    }
    &-brown2 {
        color: $color-brown2;
    }
}

.panaco-alert {
    padding-left: 2.4rem;
    position: relative;
    @include fz-14;
    color: #059669;
    white-space: nowrap;
    @media screen and (max-width: 1200px) {
        top: 0.4rem;
    }

    @media screen and (max-width: 800px) {
        padding-left: 2rem;
    }

    &::before {
        content: "";
        position: absolute;
        top: 45%;
        left: 0;
        transform: translateY(-50%);
        background-image: url(../assets/images/mua.svg);
        background-size: 100% 100%;
        width: 2rem;
        height: 2rem;
        background-repeat: no-repeat;

        @media screen and (max-width: 800px) {
            width: 1.6rem;
            height: 1.6rem;
        }
    }
}

.title-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1.2rem;

    @media screen and (max-width: 600px) {
        flex-direction: column;
        row-gap: 0.8rem;

        .btn.trans {
            border: none;
            height: unset;
            padding: 0;
            display: flex;
            align-items: center;
            overflow: visible;

            &::before {
                display: none;
            }

            .btn-ic i {
                top: 0.04rem;
                font-size: 0.8rem;

                @media screen and (max-width: 460px) {
                    font-size: 0.6rem;
                }
            }
        }
    }

    .title-sm2 {
        @media screen and (max-width: 600px) {
            text-align: center;
        }
    }
}

.title-main {
    @include fz-36;
    font-weight: 600;
    color: $color-third;
    text-align: center;

    strong {
        color: $color-pri;
    }
}

.checkBox {
    input {
        display: none;

        &:checked ~ .boxCk {
            background: $color-red;
            border-color: $color-red;
            &::before {
                opacity: 1;
            }
        }
    }

    .boxCk {
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 0.4rem;
        border: 0.1rem solid $color-black;
        display: block;
        cursor: pointer;
        transition: 0.3s linear;
        position: relative;
        &::before {
            content: "";
            width: 0.8rem;
            height: 0.8rem;
            background-image: url(../assets/images/checkW.svg);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-size: 100% 100%;
            background-repeat: no-repeat;
            opacity: 0;
            transition: 0.3s linear;
        }
    }
}

.radioBox {
    display: flex;
    align-items: center;
    column-gap: 0.8rem;

    .note-sm {
        line-height: 100%;
        color: $color-black;
    }
    input {
        display: none;

        &:checked ~ .radioCk {
            background: $color-red;
            border-color: $color-red;
            &::before {
                opacity: 1;
            }
        }
    }

    .radioCk {
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 50%;
        border: 0.1rem solid $color-black;
        display: block;
        cursor: pointer;
        transition: 0.3s linear;
        position: relative;
        &::before {
            content: "";
            width: 0.8rem;
            height: 0.8rem;
            background-image: url(../assets/images/checkW.svg);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-size: 100% 100%;
            background-repeat: no-repeat;
            opacity: 0;
            transition: 0.3s linear;
        }
    }
}

.list-address {
    row-gap: 1rem;
    // padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
}

.ic-address {
    border: none !important;
    border-radius: unset !important;
    width: 1.8rem;
    // height: 1.8rem;
    height: 100%;
    flex-shrink: 0;
    // margin-top: -0.3rem;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        filter: none;
    }
}

.item-address {
    // align-items: center !important;
    column-gap: 0.8rem;
    flex-direction: row;
    display: flex;
    @include hover {
        .link-address {
            color: $color-pri;
        }

        .ic-address {
            filter: brightness(0) saturate(100%) invert(65%) sepia(95%) saturate(4265%) hue-rotate(357deg)
                brightness(96%) contrast(107%);
        }
    }
}

.link-address {
    color: $color-text4;
    font-weight: 600;
    font-size: 1.4rem;
    transition: none;

    @media screen and (max-width: 600px) {
        @include fz-12;
    }

    @include hover {
        color: $color-pri;
    }
}

