.cart {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &_wrap {
        @media screen and (max-width: 1024px) {
            flex-direction: column;
            row-gap: var(--rowContent1);
        }
    }

    &_lf {
        width: calc(100% * 8 / 12);

        @media screen and (max-width: 1024px) {
            width: 100%;
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .woocommerce-checkout-payment {
                background-color: $color-white !important;
                border-radius: 0 !important;

                .wc_payment_method {
                }
            }
        }
    }

    &_rt {
        width: 100%;
        flex: 1;
    }

    &_box {
    }

    &_title {
        @media screen and (max-width: 600px) {
            display: none;
        }
        &--list {
            background: #faf4f1;
            padding: 2.4rem;
            display: flex;

            @media screen and (max-width: 1200px) {
                padding: 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.4rem;
            }
        }

        &--item {
            .note-text {
                text-transform: uppercase;
                color: $color-black;
                font-weight: 600;
                white-space: nowrap;
            }

            &:first-child {
                width: 35%;
            }

            &:nth-child(2) {
                width: 20%;
            }

            &:nth-child(3) {
                width: 20%;
            }

            &:nth-child(4) {
                width: 20%;
            }

            &:last-child {
                width: 100%;
                flex: 1;
            }
        }
    }

    &_gh {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent4);

            .btn {
                width: 100%;
                border-radius: 0;
                height: 5rem;

                @media screen and (max-width: 1024px) {
                    height: 4rem;
                }
            }
        }
    }

    &_content {
        padding-top: 0;
        padding-bottom: 0;
        &--list {
            background: $color-white;
            padding: 1.2em 2rem;
            max-height: 60vh;
            overflow-y: auto;
            display: flex;
            flex-direction: column;
            row-gap: 0.6rem;

            @media screen and (max-width: 1200px) {
                padding: 1.2rem 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem 1rem;
            }

            @media screen and (max-width: 600px) {
                row-gap: var(--rowContent1);
                max-height: 50vh;
            }
        }

        &--img {
            max-width: 10rem;
            width: 100%;

            img {
                width: 100% !important;
                height: 100% !important;
                object-fit: cover;
            }

            @media screen and (max-width: 1200px) {
                max-width: 8rem;
            }

            @media screen and (max-width: 1024px) {
                max-width: 6rem;
            }
        }

        &--info {
            width: 35%;
            display: flex;
            align-items: center;
            column-gap: 1.2rem;
            flex-shrink: 0;
            overflow: hidden;
            position: relative;

            &-wrap {
                display: flex;
                align-items: center;
                column-gap: 1.2rem;

                @media screen and (max-width: 600px) {
                    // width: 40%;
                    column-gap: 0.6rem;
                    width: 100%;
                    justify-content: space-between;
                    .note-sm {
                        max-width: none !important;
                        @include fz-12;
                        text-align: end;
                    }
                }
            }

            .note-sm {
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                overflow: hidden;
                max-width: 50%;
                width: 100%;
            }

            &::before {
                content: attr(data-label);
                font-weight: 700;
                display: none;
                color: $color-black;
                @include fz-16;

                @media screen and (max-width: 460px) {
                    @include fz-14;
                }
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                justify-content: space-between;
                flex-direction: column;
                row-gap: var(--rowContent1);
                align-items: start;
                &::before {
                    display: block;
                }
            }
        }

        &--price {
            width: 20%;
            flex-shrink: 0;
            overflow: hidden;
            position: relative;

            &::before {
                content: attr(data-label);
                font-weight: 700;
                display: none;
                color: $color-black;
                @include fz-16;
                text-transform: uppercase;

                @media screen and (max-width: 460px) {
                    @include fz-14;
                }
            }

            .note-sm {
                max-width: none !important;
                @include fz-12;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                justify-content: space-between;
                display: flex;
                align-items: center;
                column-gap: var(--columnContent1);

                &::before {
                    display: block;
                }
            }
        }

        &--quantity {
            width: 20%;
            flex-shrink: 0;
            overflow: hidden;
            position: relative;

            .quantity-box {
                padding: 0.4rem 0.7rem;
                column-gap: 1.2rem;
                border-color: #cccccc;

                @media screen and (max-width: 1200px) {
                    padding: 0.3rem 0.6rem;
                    column-gap: 0.6rem;
                }
            }

            .quantity-btn i {
                font-size: 1.2rem;
                color: $color-black;
            }

            .quantity-num .num {
                font-size: 1.4rem;
                color: $color-black;
            }

            &::before {
                content: attr(data-label);
                font-weight: 700;
                display: none;
                color: $color-black;
                @include fz-16;
                text-transform: uppercase;

                @media screen and (max-width: 600px) {
                    display: block;
                }

                @media screen and (max-width: 460px) {
                    @include fz-14;
                    top: 0.5rem;
                    position: relative;
                }
            }

            .note-sm {
                max-width: none !important;
                @include fz-12;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                column-gap: var(--columnContent1);
            }
        }

        &--total {
            width: 20%;
            flex-shrink: 0;
            overflow: hidden;
            position: relative;

            &::before {
                content: attr(data-label);
                font-weight: 700;
                display: none;
                color: $color-black;
                text-transform: uppercase;

                @include fz-16;
                @media screen and (max-width: 600px) {
                    display: block;
                }

                @media screen and (max-width: 460px) {
                    @include fz-14;
                    top: 0.5rem;
                    position: relative;
                }
            }

            .note-sm {
                max-width: none !important;
                @include fz-12;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                column-gap: var(--columnContent1);
            }
        }

        &--ex {
            flex-shrink: 0;
            overflow: hidden;
            width: 100%;
            flex: 1;
            text-align: end;
            position: relative;

            &::before {
                content: attr(data-label);
                font-weight: 700;
                display: none;
                text-transform: uppercase;
                color: $color-black;
                @include fz-16;
                @media screen and (max-width: 600px) {
                    display: block;
                }

                @media screen and (max-width: 460px) {
                    @include fz-14;
                    top: 0.5rem;
                    position: relative;
                }
            }

            a.remove {
                margin-left: auto;
            }

            .note-sm {
                max-width: none !important;
                @include fz-12;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                column-gap: var(--columnContent1);
            }
        }

        &--ic {
            cursor: pointer;

            @media screen and (max-width: 1200px) {
                width: 100%;
            }

            @media screen and (max-width: 600px) {
                width: fit-content;
            }

            i {
                @include fz-20;
                font-weight: 600;
            }
        }

        &--item {
            padding: 2rem;
            position: relative;
            display: flex;
            align-items: center;
            &:not(:last-child) {
                border-bottom: 0.1rem solid #faf4f1;

                @media screen and (max-width: 600px) {
                    border-color: rgba($color: #000000, $alpha: 0.1);
                }
            }

            &:last-child {
                @media screen and (max-width: 600px) {
                    padding-bottom: 0;
                }
            }
            &::before {
                content: "";
                position: absolute;
                background-image: url(../assets/images/checkbox.svg);
                width: 1.6rem;
                height: 1.6rem;
                top: 0;
                left: 0;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                z-index: 2;
                display: none;

                @media screen and (max-width: 800px) {
                    width: 1.4rem;
                    height: 1.4rem;
                }

                @media screen and (max-width: 600px) {
                    left: unset;
                    right: 0;
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0.8rem;
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
                align-items: start;
                row-gap: var(--rowContent1);
                @media screen and (max-width: 600px) {
                    border-color: rgba($color: #000000, $alpha: 0.1);
                    padding: 0 0 1.2rem 0;
                }
            }
        }
    }

    &_dh {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);

        &--total {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 1rem;
            column-gap: 1.2rem;
            .title {
                text-align: start;
            }

            .content {
                text-align: end;
            }
        }

        &--top {
            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
            }
        }

        &--notes {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &--note {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 1rem;
            column-gap: 1.2rem;
            .title {
                text-align: start;
            }

            .content {
                text-align: end;
            }
        }

        &--code {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            #coupon_code {
                height: 4.4rem;
                @media screen and (max-width: 1200px) {
                    height: 4rem;
                }

                @media screen and (max-width: 600px) {
                    height: 3.6rem;
                }
            }

            .button {
                height: 4rem;
                color: #000000 !important;
                font-weight: 600 !important;
                border-radius: 0 !important;
                @include hover {
                    background: $color-pri !important;
                    color: #ffffff !important;
                }
            }

            .form-item-ip {
                border-radius: 0;
            }
        }
    }

    &_method {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: 1.2rem;
        }

        &--item {
            &:not(:last-child) {
                padding-bottom: 1.2rem;
                border-bottom: 0.1rem solid #d9d7d6;
            }
            &-title {
                display: flex;
                justify-content: space-between;

                .checkBox {
                    position: relative;
                    top: 0rem;
                }
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: 1.2rem;
            }
        }
    }

    &_temporary {
        &--wrap {
            padding: 2.4rem;
            background: $color-white;
            display: flex;

            flex-direction: column;
            row-gap: var(--rowContent3);

            @media screen and (max-width: 1200px) {
                padding: 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem;
            }

            .cart_dh--notes {
                row-gap: var(--rowContent1);
            }

            .cart_dh--note {
                .note-text {
                    @include fz-14;
                }
            }
        }
    }

    &_pay {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .btn {
                width: 100%;
                height: 5rem;
                border-radius: 0;

                @media screen and (max-width: 1024px) {
                    height: 4rem;
                }

                &.btn-call-checkout {
                    color: $color-white;
                    @include fz-16;
                    font-size: 600;
                    &::before {
                        display: none;
                    }

                    @include hover {
                        color: $color-white;
                        z-index: 2;
                    }
                }
            }
        }

        &--info {
            padding: 1.6rem;
            background: $color-white;

            &.woocommerce-billing-fields {
                .cart_pay--info-wrap {
                    row-gap: 1.2rem;
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 1.2rem;
            }

            &-form {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);

                .form-item-ip {
                    border-radius: 0;
                    height: 5.4rem;
                }

                @media screen and (max-width: 1024px) {
                    flex-direction: row;
                    column-gap: var(--columnContent1);
                    flex-wrap: wrap;
                }

                @media screen and (max-width: 600px) {
                    flex-direction: column;
                    row-gap: var(--rowContent1);
                }

                .form-item {
                    @media screen and (max-width: 1024px) {
                        width: calc(50% - (var(--columnContent1) / 2));
                    }

                    @media screen and (max-width: 600px) {
                        width: 100%;
                    }
                }
            }

            &-rd {
                display: flex;
                align-items: center;
                column-gap: var(--columnContent2);

                @media screen and (max-width: 1024px) {
                    width: 100%;
                }
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);

                .woocommerce-billing-fields__field-wrapper {
                    display: flex;
                    flex-direction: column;
                    row-gap: 1.2rem;
                }

                #custom_label_field {
                    label {
                        margin-bottom: 0 !important;
                        @include fz-16;
                    }
                }

                .form-row {
                    padding: 0 !important;
                    margin: 0 !important;
                    display: flex;
                    flex-direction: column;
                    width: 100% !important;
                    label {
                        font-weight: 600;
                        color: $color-black;
                        @include fz-14;
                        margin-bottom: var(--rowContent0) !important;
                    }

                    .screen-reader-text {
                        position: static !important;
                        clip: unset !important;
                        height: auto !important;
                        width: 100%;
                        display: block;
                        clip-path: unset !important;
                        margin: unset !important;
                        margin-bottom: var(--rowContent0) !important;
                    }
                    .woocommerce-input-wrapper {
                    }

                    .input-text {
                        height: 5.4rem !important;
                        display: flex;
                        align-items: center;
                        padding: 1rem 2rem !important;
                        background: $color-white;
                        color: $color-black;
                        border-radius: 0 !important;
                        border: 0.1rem solid $color-gray8 !important;
                        transition: 0.5s linear;
                        width: 100%;
                        &::placeholder {
                            color: $color-gray7;
                            transition: 0.1s ease !important;
                        }

                        &:focus {
                            border: 0.1rem solid $color-pri;
                        }

                        @media screen and (max-width: 1200px) {
                            padding: 1rem 1.2rem !important;
                            height: 4rem !important;
                        }

                        @media screen and (max-width: 600px) {
                            padding: 1rem 1.2rem !important;
                            height: 3.6rem !important;
                        }
                    }

                    .select2-container {
                        height: 100%;
                        background-color: transparent;
                    }

                    .select2.select2-container {
                        width: 100% !important;
                        // margin-right: 1rem;
                    }

                    .selection {
                        width: 100%;
                        height: 100%;
                        display: block;
                    }

                    .select2-container--default .select2-selection--single {
                        border: none;
                        // border-radius: 0 !important;
                        color: white;
                        height: 5.4rem;
                        padding: 0 1.6rem;
                        border-radius: 0rem;
                        border: 0.1rem solid $color-gray4;
                        background: $color-white;
                        color: $color-text2;
                        display: flex !important;
                        align-items: center;
                        flex-direction: column;
                        justify-content: center;
                        width: 100%;

                        @media screen and (max-width: 1200px) {
                            height: 4rem;
                            padding: 0rem 1.2rem;
                        }

                        @media screen and (max-width: 800px) {
                            height: 4rem;
                            padding: 0rem 1rem;
                        }

                        @media screen and (max-width: 600px) {
                            height: 3.6rem;
                            padding: 0rem 1rem;
                        }
                    }

                    .select2-container--default .select2-selection--single .select2-selection__arrow {
                        right: 1.6rem !important;
                    }

                    .select2-container--default .select2-selection--single .select2-selection__arrow b {
                        display: none;
                    }

                    .select2-container--default .select2-selection--single .select2-selection__arrow {
                        position: absolute;

                        &::before {
                            content: "";
                            position: absolute;
                            top: 50% !important;
                            transform: translateY(-50%);
                            right: 0rem;
                            width: 2rem;
                            height: 2rem;
                            background-image: url(../assets/images/ArrDown.svg);
                            background-repeat: no-repeat;
                            background-size: 100%;
                            filter: brightness(0) saturate(100%) invert(13%) sepia(74%) saturate(513%)
                                hue-rotate(187deg) brightness(97%) contrast(98%);

                            @media screen and (max-width: 800px) {
                                width: 1.4rem;
                                height: 1.4rem;
                            }

                            @media screen and (max-width: 600px) {
                                width: 1.2rem;
                                height: 1.2rem;
                            }
                        }
                    }

                    .select2-container--default.select2-container--open.select2-container--below
                        .select2-selection--single {
                        // border-radius: 0 !important;
                    }

                    .select2.select2-container .select2-selection--multiple {
                        height: auto;
                        min-height: 34px;
                    }

                    .select2-container .select2-selection--single .select2-selection__rendered {
                        background-color: transparent;
                        color: $color-text2;
                        opacity: 1;
                        @include fz-14;
                        font-weight: 400;
                        display: inline !important;
                        @include lc(1);
                        width: 100%;
                        padding: 0 5rem 0 0rem;

                        @media screen and (max-width: 460px) {
                            @include fz-12;
                        }
                    }

                    .select2.select2-container .select2-selection--multiple .select2-selection__rendered {
                        display: block;
                        padding: 0 4px;
                    }

                    .select2-container .select2-dropdown {
                        background: transparent;
                        border: none;
                        top: 0.8rem;
                        border-radius: 0rem;
                        overflow: hidden;
                        box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.3);
                    }

                    .select2-container .select2-dropdown .select2-search {
                        padding: 0;
                    }

                    .select2-container .select2-dropdown .select2-search input {
                        outline: none !important;
                        border: none;
                        border-bottom: none !important;
                        padding: 10px 6px !important;
                        background-color: $color-black;
                        color: $color-black;
                    }

                    .select2-container .select2-dropdown .select2-results ul {
                        background: $color-white;
                        color: $color-black;
                    }

                    .select2-container
                        .select2-dropdown
                        .select2-results
                        ul
                        .select2-results__option--highlighted[aria-selected] {
                        background-color: $color-pri;

                        @include fz-14;

                        @media screen and (max-width: 460px) {
                            @include fz-12;
                        }
                    }

                    .select2-container--default .select2-results__option--selected {
                        background-color: transparent;
                        color: $color-black;
                        font-size: 1.4rem;
                        font-weight: 400;

                        @include fz-14;

                        @media screen and (max-width: 460px) {
                            @include fz-12;
                        }
                    }

                    .select2-container--default .select2-selection--single .select2-selection__arrow {
                        height: 100%;
                        border-color: #000 transparent transparent transparent;
                        // top: 1.7rem !important;
                        // top: 10%;
                        right: 0rem;
                    }

                    .select2-container--default .select2-selection--single .select2-selection__placeholder {
                        color: #c4bbbc;
                    }

                    .select2-search.select2-search--dropdown {
                        display: none !important;
                    }

                    .select2-results__option {
                        font-size: 1.4rem;
                        font-weight: 400;
                        padding: 0.8rem 1.6rem;

                        @media screen and (max-width: 600px) {
                            padding: 0.6rem 1.2rem !important;
                        }

                        @media screen and (max-width: 460px) {
                            font-size: 1.2rem;
                        }

                        span {
                            display: flex;
                            align-items: center;
                            column-gap: 0.6rem;

                            .img-flag {
                                border-radius: 50%;
                                overflow: hidden;
                                flex-shrink: 0;
                            }
                        }
                    }

                    .select2-results {
                        display: block;
                        border-radius: 0 !important;
                        overflow: hidden;
                    }

                    .re-select-wrap.white {
                        .select2-selection__rendered {
                            color: #a5a5a5 !important;
                        }
                    }

                    // .select2-results__options {
                    //     .select2-results__option {
                    //         &:first-child {
                    //             display: none;
                    //         }
                    //     }
                    // }

                    body:has(.seclect2-seach-show) {
                        .select2-search.select2-search--dropdown {
                            display: block !important;
                            background: #ffffff;
                            padding: 1.2rem 2.4rem 0.4rem;
                        }

                        .select2-container .select2-dropdown .select2-search input {
                            background-color: rgba($color: #d9d9d9, $alpha: 0.2);
                        }

                        .select2-dropdown {
                            box-shadow: 0rem 0.4rem 4rem rgba(0, 0, 0, 0.1);
                            margin-top: 2.2rem;
                        }
                        .select2-results__option {
                            padding: 0.4rem 1rem;
                            font-size: 1.4rem;

                            @media screen and (max-width: 460px) {
                                font-size: 1.2rem;
                            }
                        }
                        .select2-container
                            .select2-dropdown
                            .select2-results
                            ul
                            .select2-results__option--highlighted[aria-selected] {
                            font-size: 1.4rem;
                            line-height: 1.7;
                            color: white;
                        }
                    }
                }
            }
        }

        &--choose {
            padding: 1.6rem;
            background: $color-white;

            @media screen and (max-width: 1200px) {
                padding: 1.2rem;
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
            }

            &-form {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);

                .form-item-ip {
                    border-radius: 0;
                }

                .form-item {
                    @media screen and (max-width: 1024px) {
                        width: calc(50% - (var(--columnContent1) / 2));
                    }

                    @media screen and (max-width: 600px) {
                        width: 100%;
                    }
                }
            }

            &-box {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);

                @media screen and (max-width: 1024px) {
                    flex-direction: row;
                    column-gap: var(--columnContent1);
                    flex-wrap: wrap;
                }

                @media screen and (max-width: 600px) {
                    flex-direction: column;
                    row-gap: var(--rowContent1);
                }

                .select2-container--default .select2-selection--single {
                    border-color: #d9d9d9;
                }

                .form-item-ip {
                    border-color: #d9d9d9;

                    &:focus {
                        border-color: $color-pri;
                    }
                }
            }

            &-rd {
                display: flex;
                align-items: center;
                column-gap: var(--columnContent2);
            }
        }
    }
}

.cs {
    margin-top: var(--size-pd);
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);
    border-top: 0.1rem solid #d9d9d9;

    @media screen and (max-width: 1024px) {
        margin-top: 0 !important;
    }

    &_wrap {
        max-width: 81rem;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &_title {
        &--list {
            display: flex;
            column-gap: var(--columnContent);
            padding-bottom: 0.8rem;
            border-bottom: 0.1rem solid #d9d9d9;
        }

        &--item {
            width: fit-content;
            position: relative;
            cursor: pointer;

            &.actived {
                .note-sm {
                    color: $color-black;
                }

                &::before {
                    width: 100%;
                    left: 0;
                }
            }

            @include hover {
                .note-sm {
                    color: $color-black;
                }

                &::before {
                    width: 100%;
                    left: 0;
                }
            }

            .note-sm {
                color: #999999;
                transition: 0.5s linear;
            }

            &::before {
                content: "";
                position: absolute;
                bottom: -0.8rem;
                right: 0;
                width: 0%;
                height: 0.2rem;
                background: $color-black;
                transition: 0.3s linear;
            }
        }
    }

    &_content {
        &--list {
        }

        &--item {
            display: none;

            @keyframes anHienContent {
                0% {
                    transform: translateY(10%);
                    opacity: 0;
                }
                50% {
                    transform: translateY(5%);
                    opacity: 0.5;
                }
                100% {
                    transform: translateY(0%);
                    opacity: 1;
                }
            }

            &.showed {
                display: block;
                animation: anHienContent 0.5s linear;
            }
        }
    }
}

.success {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);

    &_wrap {
        max-width: 80rem;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        row-gap: 1.2rem;
    }

    &_title {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            background: $color-white;
            padding: 2.4rem 0;
            .cart_dh--notes {
                padding: 0 2rem;
                background: #ffffff;
                row-gap: 1.2rem;

                @media screen and (max-width: 600px) {
                    padding: 0 1.2rem;
                }
            }

            .image-qr-code {
                margin: 0 auto;
            }
        }

        &--ic {
            width: 5.4rem;
            height: 5.4rem;
            margin: 0 auto;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &--content {
            display: flex;
            flex-direction: column;

            .note-lg {
                text-align: center;
            }

            .note-text {
                text-align: center;
            }
        }
    }

    &_info {
        &--wrap {
            display: flex;
            flex-direction: column;
        }

        &--title {
            padding: 1.2rem 2rem;
            background: #faf4f1;

            @media screen and (max-width:600px) {
                padding: 1.2rem;
            }
        }

        &.other {
            .cart_dh--note {
                &:first-child {
                    padding: 0;
                    border: none;
                }

                &:last-child {
                    padding: 0;
                    border: none;
                }
            }
        }

        .cart_dh--notes {
            padding: 1.2rem 2rem;
            background: $color-white;
            row-gap: 1.2rem;

            @media screen and (max-width:600px) {
                padding: 1.2rem;
            }
        }

        .cart_dh--note {
            &:first-child {
                padding-bottom: 1.2rem;
                border-bottom: 0.1rem solid #faf4f1;
            }

            &:last-child {
                padding-top: 1.2rem;
                border-top: 0.1rem solid #faf4f1;
            }
        }
    }

    &_pro {
        &--wrap {
            display: flex;
            flex-direction: column;
        }

        &--title {
            padding: 1.2rem 2rem;
            background: #faf4f1;
        }
        &--list {
            padding: 1.2rem 0;
            background: $color-white;
        }

        &--img {
            max-width: 12rem;
            flex-shrink: 0;
            @media screen and (max-width: 1200px) {
                max-width: 10rem;
            }

            @media screen and (max-width: 800px) {
                max-width: 8rem;
            }

            @media screen and (max-width: 600px) {
                max-width: 6rem;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &--content {
            width: 100%;
            flex: 1;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--pq {
            display: flex;
            align-items: center;
            justify-content: space-between;
            column-gap: 1.2rem;
            flex-wrap: wrap;
            row-gap: var(--rowContent0);

            .sp_ct--ct-price {
                .price-normal {
                    @include fz-20;
                    font-weight: 700;
                    color: #ee1e38;

                    @media screen and (max-width: 600px) {
                        font-size: 1.4rem;
                    }
                }

                .price-line {
                    @include fz-16;
                    color: $color-black;
                    line-height: 100%;
                    top: -0.5rem;

                    @media screen and (max-width: 600px) {
                        top: -0.4rem;
                        @include fz-12;
                    }
                }
            }
        }

        &--item {
            &-wrap {
                padding: 1.2rem;
                display: flex;
                column-gap: var(--columnContent);
            }
        }
        &--name {
            .note-md {
                @media screen and (max-width: 600px) {
                    @include fz-14;
                }
            }
        }
    }

    &_btn {
        padding: 1.2rem;
        background: $color-white;

        .btn {
            width: 100%;
            height: 5rem;
            border-radius: 0;

            @media screen and (max-width: 1024px) {
                height: 4rem;
            }
        }
    }
}
