.home_said {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &--wrap {
    }

    &--list {
        .swiper-slide {
            height: auto;

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &--item {
        height: 100%;
        background: $color-white;
        border-radius: var(--radius);
        padding: 2.4rem;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        // justify-content: center;
        align-items: center;
        transition: 0.3s ease;

        @media screen and (max-width: 1200px) {
            padding: 2rem;
        }

        @media screen and (max-width: 800px) {
            padding: 1.6rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem;
        }

        @media screen and (max-width: 460px) {
            padding: 0.8rem;
        }

        @include hover {
            background: $color-second;

            .home_said--content {
                .note-lg {
                    color: $color-white;
                }
                .note-text {
                    color: $color-gray;
                }
            }
        }
    }

    &--ic {
        width: 4.8rem;
        height: 4.8rem;

        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;
        }

        @media screen and (max-width: 800px) {
            width: 3.6rem;
            height: 3.6rem;
        }

        @media screen and (max-width: 600px) {
            width: 3.2rem;
            height: 3.2rem;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover !important;
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: var(--rowContent0);

        .note-lg {
            text-align: center;
        }

        .note-text {
            text-align: center;
        }
    }
}

.home_sp {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &.bg-w {
        background: $color-white;
    }

    &.ani {
        &.showed {
            .title-main {
                clip-path: inset(0 0 0 0);
            }
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
        .title-main {
            text-align: start;
            clip-path: inset(0 100% 0 0);

            transition: 2s linear;
            @media screen and (max-width: 1200px) {
                clip-path: inset(0 0 0 0);
            }

            @media screen and (max-width: 600px) {
                text-align: center;
            }
        }
    }

    &--box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
    }

    &--top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: var(--columnContent1);
        row-gap: var(--rowContent1);
        flex-wrap: wrap;
        .btn {
            // width: 100%;
            min-width: 14rem;
            flex-shrink: 0;
            @media screen and (max-width: 1200px) {
                min-width: 12rem;
            }

            @media screen and (max-width: 800px) {
                min-width: 10rem;
            }

            @media screen and (max-width: 600px) {
                min-width: 8rem;
            }
        }

        @media screen and (max-width: 600px) {
            flex-direction: column;
        }
    }

    &--bottom {
        position: relative;

        .box-napa {
            position: absolute;
            left: 0;
            top: 0;
            padding-top: calc(100% * 240 / 1245);

            @media screen and (max-width: 1400px) {
                position: static;
                padding-top: 0;
                justify-content: center;
                margin-top: 2rem;
            }

            .swiper-pagination {
                display: none;

                @media screen and (max-width: 1400px) {
                    display: flex;
                }
            }

            .btn-navi {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                width: 4.8rem;
                height: 4.8rem;

                @media screen and (max-width: 1400px) {
                    position: static;
                    transform: unset;
                }

                @media screen and (max-width: 1200px) {
                    width: 4rem;
                    height: 4rem;
                }

                @media screen and (max-width: 800px) {
                    width: 3.6rem;
                    height: 3.6rem;
                }

                @media screen and (max-width: 600px) {
                    width: 3.2rem;
                    height: 3.2rem;
                }

                i {
                    @include fz-16;

                    @media screen and (max-width: 1200px) {
                        @include fz-14;
                    }

                    @media screen and (max-width: 600px) {
                        @include fz-12;
                    }
                }

                &.prev {
                    left: -6.4rem;
                }

                &.next {
                    right: -6.4rem;
                }
            }
        }
    }

    &--fts {
        flex: 1;
        width: 100%;

        .swiper-slide {
            width: fit-content;
            height: auto;

            &.d-item {
                padding-left: 0.8rem;
                padding-right: 0.8rem;

                @media screen and (max-width: 800px) {
                    padding-left: 0.6rem;
                    padding-right: 0.6rem;
                }
            }
        }

        &.d-wrap {
            margin-left: -0.8rem;
            margin-right: -0.8rem;

            @media screen and (max-width: 800px) {
                margin-left: -0.6rem;
                margin-right: -0.6rem;
            }
        }

        @media screen and (max-width: 600px) {
            width: 100%;
            flex: none;
            margin: 0 auto;

            .swiper {
                width: fit-content;
                margin: 0 auto;
            }
        }
    }

    &--ft {
        cursor: pointer;
        @include hover {
            .home_sp--ft-bg {
                background: $color-second;
                border-color: $color-second;
                .note-sm {
                    color: $color-white;
                }
            }
        }

        display: block;

        &-bg {
            height: 100%;
            padding: 0.5rem 0.8rem;
            border-radius: var(--radius);
            border: 0.1rem solid #e2e8f0;
            transition: 0.3s linear;
            background: transparent;

            .note-sm {
                min-width: 10rem;
                color: #808a94;
                white-space: nowrap;
                text-align: center;
                transition: 0.3s linear;

                @media screen and (max-width: 1200px) {
                    min-width: 8rem;
                }

                @media screen and (max-width: 600px) {
                    min-width: 6rem;
                }
            }
        }

        input {
            display: none;
            &:checked ~ .home_sp--ft-bg {
                background: $color-second;
                border-color: $color-second;
                .note-sm {
                    color: $color-white;
                }
            }
        }
    }
}

.home_ser {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &--title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);

        .note-text {
            text-align: center;
            max-width: 60rem;
            margin: 0 auto;
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        row-gap: var(--rowContent3);
    }

    &--item {
        // height: 100%;

        @media screen and (max-width: 800px) {
            width: calc(100% / 3);
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2);
        }

        @media screen and (max-width: 320px) {
            width: 100%;
        }
    }

    @keyframes nhoLen {
        0% {
            transform: translateY(100%);
            opacity: 0;
        }
        50% {
            transform: translateY(50%);
            opacity: 0.5;
        }
        100% {
            transform: translateY(0%);
            opacity: 1;
        }
    }

    &--link {
        display: flex;
        flex-direction: column;
        height: 100%;
        position: relative;
        border-radius: var(--radius);
        overflow: hidden;

        @include hover {
            .home_ser--box {
                height: 50%;
                background: rgba($color: #ffffff, $alpha: 1);
            }
            .home_ser--content {
                display: block;
                animation: nhoLen 0.4s linear 0.1s forwards;
            }

            .home_ser--title {
                .note-lg {
                    color: $color-pri !important;

                    &::before {
                        filter: brightness(0) saturate(100%) invert(54%) sepia(61%) saturate(5832%) hue-rotate(7deg)
                            brightness(103%) contrast(102%);
                    }
                }
            }
        }
    }

    &--img {
        padding-top: calc(100% * 400 / 384);
        width: 100%;
        position: relative;
        overflow: hidden;
        display: flex;
        flex-direction: column;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100% !important;
            height: 100% !important;
            object-fit: cover !important;
        }
    }

    &--box {
        padding: 2.4rem;
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
        z-index: 2;
        height: 30%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        background: rgba($color: #ffffff, $alpha: 0.8);
        row-gap: 1.2rem;
        transition: 0.4s linear;

        @media screen and (max-width: 1200px) {
            padding: 2rem;
            height: 25%;
        }

        @media screen and (max-width: 800px) {
            padding: 1.2rem;
            height: 100%;
            position: relative;
            left: unset;
            bottom: unset;
        }
    }

    &--content {
        display: none;
        opacity: 0;
        .note-text {
            display: -webkit-box;
            -webkit-line-clamp: 5;
            -webkit-box-orient: vertical;
            overflow: hidden;
            color: $color-text;
        }
    }

    &--title {
        display: block;

        .note-lg {
            padding-right: 3rem;
            position: relative;
            width: 100%;
            color: $color-third;
            &::before {
                content: "";
                width: 2.4rem;
                height: 2.4rem;
                background-size: 100% 100%;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                background-image: url(../assets/images/icArrsvg.svg);

                @media screen and (max-width: 800px) {
                    width: 2rem;
                    height: 2rem;
                }
            }

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;

            @media screen and (max-width: 800px) {
                padding-right: 2.4rem;
                @include fz-16;
            }
        }
    }
}

.home_nb {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    .container {
        max-width: 164.5rem;

        @media screen and (max-width: 1500px) {
            max-width: 143rem;
        }

        @media screen and (max-width: 1400px) {
            max-width: 133rem;
        }

        @media screen and (max-width: 1300px) {
            max-width: 123rem;
        }

        @media screen and (max-width: 1200px) {
            max-width: 100rem;
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);

        .home_ser--title {
            .note-text {
                max-width: 38rem;
            }
        }
    }

    &--list {
        position: relative;
        .swiper-slide {
            display: flex;
            justify-content: center;
            width: 80rem !important;

            @media screen and (max-width: 1400px) {
                width: 65rem !important;
            }

            @media screen and (max-width: 1200px) {
                width: 60rem !important;
            }

            @media screen and (max-width: 1000px) {
                width: 100% !important;
                display: block !important;
            }
        }

        .swiper {
            padding: 0.5rem 0;
        }

        .swiper-slide-active {
            padding: 0;
            display: flex;
            justify-content: center;

            .home_nb--item {
            }

            .home_nb--img {
                &::before {
                    opacity: 0;
                }
            }

            .home_nb--box {
                transform: translateY(0%);
                opacity: 1;
            }
        }

        .box-navi {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            padding-top: calc(100% * 250 / 800);
            max-width: 80rem;
            z-index: 100;
            .btn-navi {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);

                width: 4.8rem;
                height: 4.8rem;

                @media screen and (max-width: 1200px) {
                    width: 4rem;
                    height: 4rem;
                }

                @media screen and (max-width: 800px) {
                    width: 3.6rem;
                    height: 3.6rem;
                }

                @media screen and (max-width: 600px) {
                    width: 2.6rem;
                    height: 2.6rem;
                }

                i {
                    @include fz-16;

                    @media screen and (max-width: 1200px) {
                        @include fz-14;
                    }

                    @media screen and (max-width: 600px) {
                        @include fz-12;
                    }
                }

                &.prev {
                    left: -2rem;

                    @media screen and (max-width: 1000px) {
                        left: -1.2rem;
                    }
                }

                &.next {
                    right: -2rem;

                    @media screen and (max-width: 1000px) {
                        right: -1.2rem;
                    }
                }
            }

            @media screen and (max-width: 1500px) {
                padding-top: calc(100% * 290 / 800);
            }

            @media screen and (max-width: 1400px) {
                max-width: 65rem;
                padding-top: calc(100% * 205 / 650);
            }

            @media screen and (max-width: 1300px) {
                max-width: 65rem;
                padding-top: calc(100% * 225 / 650);
            }

            @media screen and (max-width: 1200px) {
                max-width: 60rem;
                padding-top: calc(100% * 235 / 600);
            }

            @media screen and (max-width: 1000px) {
                max-width: unset;
                padding-top: calc(100% * 630 / 1000);
            }

            @media screen and (max-width: 600px) {
                max-width: unset;
                padding-top: calc(100% * 635 / 1000);
            }
        }
    }

    &--item {
        position: relative;
        width: 100%;
        height: 100%;
        transform: scale(1);
        border-radius: var(--radius) !important;
        overflow: hidden !important;
    }

    &--img {
        padding-top: calc(100% * 500 / 800);
        width: 100%;
        position: relative;
        overflow: hidden;
        display: block;
        border-radius: var(--radius);

        @media screen and (max-width: 600px) {
            border-top-left-radius: var(--radius) !important;
            border-top-right-radius: var(--radius) !important;
            border-bottom-left-radius: 0 !important;
            border-bottom-right-radius: 0 !important;
        }

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100% !important;
            height: 100% !important;
            object-fit: cover !important;
            border-radius: var(--radius) !important;
            overflow: hidden !important;

            @media screen and (max-width: 600px) {
                border-top-left-radius: var(--radius) !important;
                border-top-right-radius: var(--radius) !important;
                border-bottom-left-radius: 0 !important;
                border-bottom-right-radius: 0 !important;
            }
        }

        &::before {
            content: "";
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            transition: 1s linear;

            background: rgba($color: #000000, $alpha: 0.5);
        }
    }

    &--box {
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
        background: #232f9ad9;
        padding: 2.4rem;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);
        transform: translateY(100%);
        opacity: 0;
        transition: 1s ease;
        .home_ser--content {
            display: block;
            opacity: 1;

            .note-text {
                color: $color-white;
                display: block;
                max-height: 8rem;
                overflow-y: auto;

                @media screen and (max-width: 600px) {
                    max-height: unset;
                }
            }
        }

        .home_ser--title {
            .note-lg {
                color: $color-white;

                &::before {
                    filter: brightness(0) saturate(100%) invert(54%) sepia(61%) saturate(5832%) hue-rotate(7deg)
                        brightness(103%) contrast(102%);
                }
            }
        }

        @media screen and (max-width: 1400px) {
            padding: 1.8rem;
        }

        @media screen and (max-width: 1200px) {
            padding: 1.2rem;
        }

        @media screen and (max-width: 600px) {
            position: relative;
            bottom: unset;
            left: unset;
            // transform: translateY(-100%);
        }
    }
}

.home_new {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
    }

    &--box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);

        .btn {
            margin: 0 auto;
        }
    }

    &--list {
        .swiper-slide {
            @media screen and (max-width: 1200px) {
                width: calc(100% / 3);
            }

            @media screen and (max-width: 600px) {
                width: calc(100% / 2);
            }

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &--item {
    }
}

.home_mail {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    margin-top: var(--size-pd);
    background: #3d4cd8;

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
    }

    &--title {
        .title-main {
            color: $color-white;

            @media screen and (max-width: 600px) {
                @include fz-20;
            }

            @media screen and (max-width: 360px) {
               font-size: 1.4rem;
            }
        }

        .note-text {
            color: $color-white;
            text-align: center;
            @media screen and (max-width: 600px) {
                @include fz-14;
            }
        }
    }

    &--form {
        position: relative;
        max-width: 60rem;
        width: 100%;
        margin: 0 auto;

        .btn {
            position: absolute;
            top: 50%;
            right: 0.3rem;
            transform: translateY(-50%);
            height: 85%;

            @media screen and (max-width: 460px) {
                width: fit-content;
                min-width: unset;
            }
        }

        input {
            padding-right: 20rem;

            @media screen and (max-width: 1200px) {
                padding-right: 14rem;
            }

            @media screen and (max-width: 460px) {
                padding-right: 9rem;
            }
        }
    }
}
