.about_nt {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);
    overflow: hidden;
    background: #f7f7f7;

    @media screen and (max-width: 600px) {
        padding-top: var(--size-pd);
        padding-bottom: var(--size-pdl);
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &--title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        max-width: 72rem;
        width: 100%;
        margin: 0 auto;
        .title-main {
            text-align: center;
            color: $color-black;
        }

        .note-text {
            text-align: center;
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
        .title-main {
            text-align: center;
        }
    }

    &--img {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

.about_ts {
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);
    overflow: hidden;
    &--wrap {
        display: flex;
        flex-direction: column;
    }

    &--title {
        padding: 0.2rem 4rem 0.2rem 0.2rem;
        background: #f5f5f5;
        position: absolute;
        top: -0.1rem;
        left: -0.1rem;
        width: fit-content;
        z-index: 2;
        .title-main {
            text-align: start;
            color: $color-black;
            width: fit-content;

            @media screen and (max-width: 460px) {
                text-align: center;
                width: 100%;
            }
        }

        @media screen and (max-width: 1200px) {
            padding: 0.4rem;
        }

        @media screen and (max-width: 460px) {
            position: relative;
            left: 0%;
            top: 0;

            width: 100%;
        }
    }

    &--imgs {
    }

    &--top {
        position: relative;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);
    }

    &--img {
        padding-top: calc(100% * 435 / 1216);
        width: 100%;
        position: relative;
        display: block;
        overflow: hidden;

        @media screen and (max-width: 600px) {
            padding-top: calc(100% * 320 / 600);
        }

        img {
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
            position: absolute;
        }
    }

    &--bottom {
        &-wrap {
            display: flex;

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }
    }

    &--lf {
        width: calc(100% * 4 / 12);

        &-list {
            display: flex;
            flex-direction: column;
        }

        &-item {
            padding: 3.2rem;
            border-left: 0.1rem solid #e0e0e0;
            border-bottom: 0.1rem solid #e0e0e0;
            color: $color-black;
            font-weight: 600;
            cursor: pointer;
            transition: 0.3s linear;
            position: relative;

            @media screen and (max-width: 1200px) {
                padding: 2.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.4rem 1rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem;

                border-left: 0.1rem solid #e0e0e0;
                border-bottom: 0.1rem solid #e0e0e0;
                border-right: 0.1rem solid #e0e0e0;

                &:last-child {
                    border-bottom: 0;
                }
            }

            &::after {
                content: "";
                height: 100%;
                background: $color-pri;
                position: absolute;
                top: 0;
                left: 0;
                width: 0;
                transition: 0.5s ease;
                z-index: -1;
            }

            &::before {
                content: "";
                top: 50%;
                right: 3.2rem;
                transform: translateY(-50%);
                position: absolute;
                width: 2rem;
                height: 2rem;
                background-image: url(../assets/images/ArrTs.svg);
                background-repeat: no-repeat;
                background-size: 100% 100%;
                opacity: 0;
                transition: 0.5s ease;

                @media screen and (max-width: 1200px) {
                    right: 2.4rem;
                }

                @media screen and (max-width: 800px) {
                    right: 1.2rem;
                    width: 1.6rem;
                    height: 1.6rem;
                }

                @media screen and (max-width: 600px) {
                    right: 1.2rem;

                    display: none;
                }
            }

            .note-md {
                padding-right: 3.2rem;
                @media screen and (max-width: 1200px) {
                    padding-right: 2.4rem;
                }

                @media screen and (max-width: 800px) {
                    padding-right: 2rem;
                    @include fz-14;
                }

                @media screen and (max-width: 600px) {
                    padding-right: 0rem;
                }
            }

            @include hover {
                color: $color-white;

                &::before {
                    opacity: 1;
                }

                &::after {
                    width: 100%;
                }
            }

            &.actived {
                color: $color-white;

                &::before {
                    opacity: 1;
                }

                &::after {
                    width: 100%;
                }
            }
        }

        @media screen and (max-width: 800px) {
            width: calc(100% * 2.5 / 12);
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &--rt {
        width: 100%;
        flex: 1;

        &-list {
            height: 100%;
            padding: 4.8rem 6.4rem;
            background: $color-brown;

            @media screen and (max-width: 1200px) {
                padding: 3.2rem 4.8rem;
            }

            @media screen and (max-width: 800px) {
                padding: 2rem 3.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 2rem 1.2rem;
            }
        }

        &-item {
            display: none;

            @keyframes anHienTs {
                0% {
                    opacity: 0;
                    transform: translateX(-5%);
                }
                50% {
                    opacity: 0.5;
                    transform: translateX(-2.5%);
                }
                100% {
                    opacity: 1;
                    transform: translateX(0%);
                }
            }

            @media screen and (max-width: 600px) {
                @keyframes anHienTs {
                    0% {
                        opacity: 0;
                        transform: translateY(50%);
                    }
                    50% {
                        opacity: 0.5;
                        transform: translateY(25%);
                    }
                    100% {
                        opacity: 1;
                        transform: translateY(0%);
                    }
                }
            }

            &.showed {
                display: block;
                animation: anHienTs 0.6s linear;
            }
        }

        &-content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .title-main {
                text-align: start;
                color: $color-white;
            }

            .note-text {
                color: $color-white;
            }
        }
    }
}

.about_dg {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;

    .mona-content p {
        margin: 0 !important;
    }

    &--wrap {
        align-items: center;

        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: var(--rowContent1);
        }
    }

    &--tt {
        .around-tt {
            position: absolute;
            z-index: 2;

            @keyframes phongTo {
                0% {
                    opacity: 0;
                }
                50% {
                    opacity: 0.8;
                }
                100% {
                    opacity: 0;
                }
            }

            &:first-child {
                width: 1rem;
                height: 1rem;
                border-radius: 50%;
                background: linear-gradient(90deg, #f6e3b3 0%, #c2945d 53.73%, #e1c491 80.28%, #f6e3b3 100%);
                top: 5rem;
                left: 5rem;
                animation: phongTo 2s linear infinite;
                z-index: 3;

                @media screen and (max-width: 460px) {
                    width: 1rem;
                    height: 1rem;
                    left: 4rem;
                    top: 2rem;
                }
            }

            &:nth-child(2) {
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                background: linear-gradient(90deg, #f6e3b3 0%, #c2945d 53.73%, #e1c491 80.28%, #f6e3b3 100%);
                bottom: 5rem;
                left: 1rem;
                animation: phongTo 2s 1s linear infinite alternate;
                z-index: 3;

                @media screen and (max-width: 1200px) {
                    width: 1.5rem;
                    height: 1.5rem;
                    bottom: 6rem;
                    left: 2rem;
                }
            }

            &:last-child {
                width: 4rem;
                height: 4rem;
                border-radius: 50%;
                background: linear-gradient(90deg, #f6e3b3 0%, #c2945d 53.73%, #e1c491 80.28%, #f6e3b3 100%);
                top: 8rem;
                right: 3rem;
                animation: phongTo 2s linear infinite alternate-reverse;
                z-index: 2;

                @media screen and (max-width: 1200px) {
                    width: 2rem;
                    height: 2rem;
                    right: 2.5rem;
                }

                @media screen and (max-width: 460px) {
                    width: 2rem;
                    height: 2rem;
                    right: 1.5rem;
                }
            }
        }
    }

    &--lf {
        width: calc(100% * 4 / 12);

        @media screen and (max-width: 800px) {
            width: 100%;
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            max-width: 80%;

            @media screen and (max-width: 800px) {
                max-width: none;
            }

            .title-main {
                text-align: start;

                @media screen and (max-width: 600px) {
                    text-align: center;
                }
            }

            .note-text{
                @media screen and (max-width: 600px) {
                    text-align: center;
                }
            }
        }
    }

    &--img {
        padding-top: 100%;
        width: 100%;
        display: block;
        overflow: hidden;
        position: relative;
        border-radius: 50%;
        z-index: 2;
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &--rt {
        flex: 1;
        width: 100%;

        --pd: 4.8rem;

        @media screen and (max-width: 1200px) {
            --pd: 3.2rem;
        }

        @media screen and (max-width: 900px) {
            --pd: 2.4rem;
        }

        &-wrap {
            padding: var(--pd);
            background: $color-white;
            border-radius: var(--radius);
            position: relative;

            @media screen and (max-width: 600px) {
                padding: 1.2rem 2.4rem;
            }

            .btn-navi {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                &.prev {
                    // left: 0;
                    left: calc(var(--pd) / -3);
                }

                &.next {
                    // left: 0;
                    right: calc(var(--pd) / -3);
                }
            }
        }

        &-item {
            display: flex;
            column-gap: var(--columnContent2);
            align-items: center;
            width: 100%;

            @media screen and (max-width: 460px) {
                flex-direction: column;
                row-gap: var(--rowContent1);
            }

            &-lf {
                width: 40%;

                @media screen and (max-width: 460px) {
                    width: 100%;
                }
            }

            &-info {
                display: flex;
                flex-direction: column;
                row-gap: 0.2rem;

                .note-lg {
                    strong {
                        font-weight: 700;
                        color: $color-third;
                    }

                    @media screen and (max-width: 460px) {
                        @include fz-14;
                    }
                }
            }

            &-rt {
                width: 100%;
                flex: 1;

                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
            }

            &-box {
                max-width: 31rem;
                width: 100%;
                flex-shrink: 0;
                margin: auto;
                position: relative;

                @media screen and (max-width: 460px) {
                    max-width: 15rem;
                }
            }
        }
    }

    &--around {
        background: #0cb582;
        width: 18rem;
        height: 18rem;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;

        @media screen and (max-width: 1200px) {
            width: 14rem;
            height: 14rem;
        }

        @media screen and (max-width: 900px) {
            width: 10rem;
            height: 10rem;
        }

        @media screen and (max-width: 800px) {
            width: 16rem;
            height: 16rem;
        }

        @media screen and (max-width: 700px) {
            width: 14rem;
            height: 14rem;
        }

        @media screen and (max-width: 600px) {
            width: 12rem;
            height: 12rem;
        }

        @media screen and (max-width: 500px) {
            width: 10rem;
            height: 10rem;
        }

        @keyframes lanToa {
            0% {
                transform: translate(-50%, -50%) scale(1);
                opacity: 0;
            }

            50% {
                transform: translate(-50%, -50%) scale(1.4);
                opacity: 0.5;
            }

            100% {
                transform: translate(-50%, -50%) scale(1.6);
                opacity: 0;
            }
        }

        &-bg {
            position: relative;
            z-index: -1;
            width: 100%;
            height: 100%;
            &::before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background: #8cdcc4;
                opacity: 0;
                z-index: 1;
                animation: lanToa 1.8s linear infinite forwards;
            }

            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background: #8cdcc4;

                opacity: 0;
                z-index: 1;
                animation: lanToa 1.8s 0.9s linear infinite forwards;
            }
        }
    }
}

.about_over {
    overflow: hidden;
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &.ani {
        &.showed {
            .about_over--top-lf-wrap {
                &::before {
                    opacity: 1;
                    width: 90%;
                }

                &::after {
                    visibility: visible;
                    transform: translateY(0);
                }

                .about_over--top-lf-img {
                    transform: translateX(0);
                    opacity: 1;
                }
            }

            .about_over--top-rt-wrap {
                .title-main {
                    clip-path: inset(0 0 0 0);
                    opacity: 1;
                }

                .about_over--top-rt-content {
                    transform: translateY(0%);
                    opacity: 1;
                }
            }
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent6);

        @media screen and (max-width: 800px) {
            row-gap: var(--rowContent3);
        }
    }

    &--top {
        &-lf {
            &-wrap {
                padding: 2rem 3.5rem 0rem 0rem;
                position: relative;
                // overflow: hidden;
                &::before {
                    content: "";
                    width: 0%;
                    height: 25.4rem;
                    background: $color-pri;
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: -1;
                    transition: 1.2s ease;
                    opacity: 0;
                    pointer-events: none;

                    @media screen and (max-width: 1200px) {
                        opacity: 1;
                        width: 90%;
                    }

                    @media screen and (max-width: 800px) {
                        display: none;
                    }
                }

                &::after {
                    content: "";
                    width: 40%;
                    height: 9.6rem;
                    background-image: url(../assets/images/overbg.svg);
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    position: absolute;
                    bottom: -2rem;
                    left: -4rem;
                    z-index: -1;
                    animation: phongTo 4s linear infinite;
                    transition: 1.2s ease;
                    transform: translateY(100%);
                    visibility: hidden;
                    pointer-events: none;
                    @media screen and (max-width: 1200px) {
                        visibility: visible;

                        transform: translateY(0%);
                    }

                    @media screen and (max-width: 800px) {
                        display: none;
                    }
                }

                @media screen and (max-width: 1200px) {
                    padding: 2rem 2.4rem 0rem 0rem;
                }

                @media screen and (max-width: 800px) {
                    padding: 0rem;
                }
            }

            &-img {
                padding-top: calc(100% * 509 / 552);
                position: relative;
                width: 100%;
                overflow: hidden;
                display: block;
                transform: translateX(-100%);
                opacity: 0;
                transition: 1.2s ease;

                @media screen and (max-width: 1200px) {
                    transform: translateX(0%);
                    opacity: 1;
                }

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            @media screen and (max-width: 800px) {
                width: 100%;
            }
        }

        &-rt {
            @media screen and (max-width: 800px) {
                width: 100%;
            }
            &-wrap {
                overflow: hidden;
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);
                justify-content: space-between;
                height: 100%;
                .title-main {
                    text-align: start;
                    clip-path: inset(0 100% 0 0);
                    opacity: 0;
                    transition: 3s ease;

                    @media screen and (max-width: 1200px) {
                        clip-path: inset(0 0 0 0);
                        opacity: 1;
                    }
                }
            }

            &-content {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent);
                transition: 2s ease;
                transform: translateY(100%);
                opacity: 0;

                @media screen and (max-width: 1200px) {
                    transform: translateY(0%);
                    opacity: 1;
                }
            }
        }

        &-wrap {
            @media screen and (max-width: 800px) {
                flex-direction: column-reverse;
                row-gap: var(--rowContent3);
            }
        }
    }

    &--bottom {
        &-wrap {
            @media screen and (max-width: 800px) {
                flex-direction: column;
                row-gap: var(--rowContent2);
            }
        }

        &-lf {
            @media screen and (max-width: 800px) {
                width: 100%;
            }
            &-wrap {
                .note-text {
                    strong {
                        color: $color-third;
                        font-weight: 600;
                    }
                }
            }
        }

        &-rt {
            @media screen and (max-width: 800px) {
                width: 100%;
            }
            &-wrap {
                .note-text {
                    strong {
                        color: $color-third;
                        font-weight: 600;
                    }
                }
            }
        }
    }

    &--note {
        padding: 2rem 1.8rem;
        background: #fdf2e9;
        box-shadow: 3px 3px 12px 0px #0000001a;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
        // margin: var(--rowContent4) 0;

        @media screen and (max-width: 1200px) {
            padding: 1.6rem 1.2rem;
            row-gap: var(--rowContent1);
        }

        &-top {
        }

        &-bottom {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            .list-address {
                max-width: 43rem;
                width: 100%;
            }

            .item-address {
                align-items: center;
                column-gap: 0.8rem;
            }

            .link-address {
                @include fz-16;
            }
        }
    }
}
