.progress {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_list {
        .swiper-slide {
            height: auto;

            @media screen and (max-width: 360px) {
                width: calc(100% / 2);
            }

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &_item {
        height: 100%;

        &--wrap {
            padding: 1.2rem;
            background: $color-brown2;
            height: 100%;
            display: flex;
            flex-direction: column;
            row-gap: 1.2rem;
            overflow: hidden;
            border-radius: var(--radius);
            transition: 0.3s linear;
            .title-mn {
                text-align: center;

                @media screen and (max-width: 800px) {
                    @include fz-16;
                }
            }

            @include hover {
                background: $color-third;
            }

            @media screen and (max-width: 800px) {
                padding: 0.8rem;
            }
        }

        &--img {
            padding-top: calc(100% * 167 / 254);
            width: 100%;
            position: relative;
            display: block;
            overflow: hidden;
            border-radius: 0.6rem;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.more {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);
    margin-top: var(--size-pdl);
    margin-bottom: var(--size-pd);
    background: $color-brown2;

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .title-main {
            color: $color-white;
        }
    }

    &_list {
        .swiper-slide {
            height: auto;

            @media screen and (max-width: 800px) {
                width: calc(100% / 3);
            }

            @media screen and (max-width: 600px) {
                width: calc(100% / 2);
            }

            @media screen and (max-width: 320px) {
                width: calc(100% / 1);
            }
        }

        .swiper-pagination {
            margin-top: 3rem;
        }
    }

    &_item {
        height: 100%;

        &--wrap {
            height: 100%;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            background: $color-white;
            padding: 1.2rem;
            border-radius: var(--radius);

            transition: 0.3s linear;

            @include hover {
                background: $color-five;
            }

            @media screen and (max-width: 1200px) {
                padding: 1rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0.8rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.6rem;
            }
        }

        &--img {
            padding-top: calc(100% * 264 / 357);
            width: 100%;
            display: block;
            overflow: hidden;
            border-radius: 0.6rem;
            position: relative;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 100%;
                height: 100%;
                transform: translate(-50%, -50%);
                object-fit: cover;
            }
        }

        &--content {
            display: flex;
            flex-direction: column;
            row-gap: 1rem;
            max-width: 32rem;
            width: 100%;
            margin: 0 auto;
            .note-lg {
                text-align: center;
                color: $color-brown2;

                @media screen and (max-width: 800px) {
                    @include fz-16;
                }
            }

            .note-text {
                text-align: center;
            }
        }
    }
}

.cooperate {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .box-napa {
            margin-top: var(--rowContent);
        }

        .title-main {
            color: $color-black;
        }

        .swiper {
            overflow: visible;

            @media screen and (max-width: 800px) {
                overflow: hidden;
            }
        }

        .swiper-slide {
            width: 35% !important;

            @media screen and (max-width: 1400px) {
                width: 30% !important;
            }

            @media screen and (max-width: 800px) {
                width: 50% !important;
            }

            @media screen and (max-width: 600px) {
                width: 60% !important;
            }

            @media screen and (max-width: 460px) {
                width: 70% !important;
            }

            @media screen and (max-width: 360px) {
                width: 80% !important;
            }

            @media screen and (max-width: 320px) {
                width: 85% !important;
            }
        }

        .pro_dm--link-img {
            padding-top: calc(100% * 183 / 384);
            border: none;
            border-radius: 0;

            &::before {
                background: rgba($color: #000000, $alpha: 0.2);
            }
        }

        .pro_dm--link-bottom {
            .note-lg {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                @media screen and (max-width: 800px) {
                    @include fz-16;
                }
            }
        }
    }
}

.advise {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    position: relative;
    overflow: hidden;
    &::before {
        content: "";
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background-image: url(../assets/images/bgtv.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        z-index: -1;
    }

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_form {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);

        max-width: 60rem;
        margin: 0 auto;
    }
}

.faq {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        max-width: 81.2rem;
        margin: 0 auto;
        border-radius: var(--radius);
        padding: 1.2rem 2.4rem;
        background: $color-white;
        .title-mn {
            text-align: center;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem 1.2rem;
        }
    }

    &_list {
        display: flex;
        flex-direction: column;
    }

    &_item {
        --col: 2.4rem;
        --width: 2.4rem;
        padding: 1.6rem 0;
        border-top: 0.1rem solid #eaecf0;

        @media screen and (max-width: 800px) {
            --width: 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem 0;
        }

        &:last-child {
            padding-bottom: 0;
        }

        &.actived {
            .faq_item--ic {
                border-color: $color-text;

                .line {
                    background: $color-text;

                    &:last-child {
                        transform: translate(-50%, -50%) rotate(0deg);
                    }
                }
            }
        }
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--title {
            display: flex;
            justify-content: space-between;
            // align-items: center;
            column-gap: var(--col);
            cursor: pointer;
            .note-text {
            }
        }

        &--ic {
            flex-shrink: 0;
            width: var(--width);
            height: var(--width);
            border-radius: 50%;
            overflow: hidden;
            border: 0.2rem solid $color-third;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            // top: -0.1rem;
            .line {
                width: 40%;
                display: block;
                height: 0.2rem;
                background: $color-third;
                border-radius: 1rem;
                position: absolute;
                top: 50%;
                left: 50%;
                cursor: pointer;
                transition: 0.5s ease;
                &:first-child {
                    transform: translate(-50%, -50%);
                }

                &:last-child {
                    transform: translate(-50%, -50%) rotate(90deg);
                }
            }
        }

        &--content {
            padding-right: calc(var(--col) + var(--width));
            display: none;
        }
    }
}

.important {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        position: relative;

        .pro_des--wrap {
            padding: 0;
            background: transparent;
        }

        .pro_des--btn {
            .btn {
                position: relative;
                transform: unset;
                top: unset;
                left: unset;
                margin: 2rem auto 0;
            }
        }
    }
}

.question {
    &_wrap {
        max-width: 90.7rem;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);

        .comment_cmt--item-info-top {
            column-gap: 1.6rem;
        }
    }
}

.commit {
    margin-top: var(--size-pd);
    // margin-bottom: var(--size-pd);
    background: $color-white;
    overflow: hidden;
    &_title {
        padding: 1.6rem 5rem;
        background: $color-pri;
        border-radius: var(--radius);
        .title-main {
            text-align: start;
            color: $color-white;

            @media screen and (max-width: 800px) {
                text-align: center;
            }
        }

        @media screen and (max-width: 1200px) {
            padding: 1.2rem 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem;
        }
    }

    &_wrap {
        // align-items: center;

        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: var(--rowContent3);
        }
    }

    &_list {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
    }

    &_item {
        &--ic {
            width: 4rem;
            height: 4rem;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;

            @media screen and (max-width: 1200px) {
                width: 3.2rem;
                height: 3.2rem;
            }

            @media screen and (max-width: 600px) {
                width: 2.6rem;
                height: 2.6rem;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &--wrap {
            display: flex;
            column-gap: 1rem;
            align-items: center;
            .note-md {
                color: $color-third;
                font-weight: 600;
                line-height: 1.3;
                strong {
                    color: $color-brown2;
                    font-weight: 700;
                }

                @media screen and (max-width: 600px) {
                    @include fz-16;
                }
            }
        }
    }

    &_lf {
        width: calc(100% * 6 / 12);
        display: flex;
        align-items: center;
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);
            padding: 8rem 0;
            width: 100%;
            @media screen and (max-width: 1200px) {
                padding: 4rem 0;
            }

            @media screen and (max-width: 800px) {
                padding: 2rem 0 0 0;
            }

            // @media screen and (max-width: 800px) {
            //     padding: 4rem 0;
            // }
        }

        @media screen and (max-width: 1200px) {
            width: calc(100% * 5.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }
    }

    &_rt {
        width: 100%;
        flex: 1;

        &--wrap {
            margin-right: calc((100vw - 120rem) / -2);
            height: 100%;

            @media screen and (max-width: 1200px) {
                margin-right: -3rem;
            }

            @media screen and (max-width: 800px) {
                margin-right: 0rem;
            }
        }

        &--img {
            padding-top: calc(100% * 669 / 848);
            width: 100%;
            height: 100%;
            overflow: hidden;
            display: block;
            position: relative;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.said {
    padding-top: 20rem;
    overflow: hidden;
    padding-bottom: var(--size-pd);

    @media screen and (max-width: 1200px) {
        padding-top: 22vw;
    }

    @media screen and (max-width: 900px) {
        padding-top: 18vw;
    }

    @media screen and (max-width: 800px) {
        padding-top: 22vw;
    }

    @media screen and (max-width: 600px) {
        padding-top: var(--size-pd);
    }

    &_wrap {
        display: flex;
        align-items: end;
        justify-content: space-between;
        column-gap: 2rem;
        position: relative;

        &::before {
            content: "";
            top: 0;
            left: calc((100vw - 120rem) / -2);
            width: calc(100vw + ((100vw - 120rem) / 4));
            height: 100%;
            position: absolute;
            // background: ;
            background: linear-gradient(269.93deg, #232f9a 54.26%, rgba(122, 129, 196, 0) 99.92%);
            z-index: -1;
            background-size: 100% 100%;

            @media screen and (max-width: 1200px) {
                // left: -1.5rem;
                left: 0;
                width: 100%;
            }

            @media screen and (max-width: 600px) {
                background: $color-second;
            }
        }

        @media screen and (max-width: 600px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &_lf {
        flex: 1;
        width: 100%;

        &--wrap {
            position: relative;
        }

        &--box {
            max-width: 40.9rem;
            width: 100%;
            margin-left: auto;
            position: absolute;
            bottom: 0;
            right: 0;

            @media screen and (max-width: 600px) {
                position: static;
                margin: 0 auto;
                max-width: 25rem;
            }
        }
    }

    &_img {
        padding-top: calc(100% * 425 / 409);
        width: 100%;
        display: block;
        position: relative;
        overflow: hidden;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }

    &_rt {
        width: calc(100% * 7 / 12);

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &--wrap {
            padding: 7rem 0;

            @media screen and (max-width: 1200px) {
                padding: 5rem 0;
            }

            @media screen and (max-width: 800px) {
                padding: 3rem 0;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem 1rem 0 1rem;
            }
            .title-mn {
                @media screen and (max-width: 800px) {
                    font-size: 1.6rem;
                }
            }
        }
    }
}

.install {
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);

    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);
    background: #e8edfb;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_tabs {
        display: flex;
        align-items: center;
        column-gap: var(--columnContent2);
        width: fit-content;
        margin: 0 auto;
        justify-content: center;
        flex-wrap: wrap;
        row-gap: var(--rowContent1);

        @media screen and (max-width: 460px) {
            flex-direction: column;
            width: 100%;
            // max-width: 60%;
        }
    }

    &_tab {
        padding: 0.6rem 1.6rem;
        background: $color-white;
        border-radius: var(--radius);
        border: 0.2rem solid #e8e8e8;
        font-weight: 700;
        color: $color-third;
        transition: 0.5s ease;
        white-space: nowrap;
        cursor: pointer;
        text-align: center;
        @include hover {
            background: $color-second;
            border-color: $color-second;
            color: $color-white;
        }

        &.actived {
            background: $color-second;
            border-color: $color-second;
            color: $color-white;
        }

        @media screen and (max-width: 460px) {
            width: 100%;
        }
    }

    &_list {
    }

    &_item {
        display: none;

        &.showed {
            display: block;
            animation: anHienContent 0.8s linear;
        }
    }

    &_content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);

        .note-lg {
            text-align: center;
        }
    }
}

.ser_tabs {
    padding-bottom: var(--size-pd);
    border-bottom: 0.1rem solid #d9d9d9;
    display: flex;
    flex-direction: column;
    align-items: center;
    &--wrap {
        .swiper {
            width: fit-content;
            margin: auto;
        }

        .swiper-slide {
            width: fit-content;
        }

        .swiper-scrollbar {
            height: 0.3rem;
            margin-top: 0.5rem;
            .swiper-scrollbar-drag {
                background: $color-pri;
            }
        }

        .sp_des--tab-link {
            padding: 1rem 2rem;
            border-color: #e4e2e2;
            white-space: nowrap;

            @media screen and (max-width: 1200px) {
                padding: 0.8rem 1.6rem;
            }
        }

        .sp_des--tabs {
        }
    }
}

.why {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    position: relative;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);
    }

    &_title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent3);

        &--list {
            display: flex;
            column-gap: var(--columnContent);
            row-gap: var(--rowContent1);

            width: fit-content;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 auto;
        }

        &--ic {
            width: 4rem;
            height: 4rem;
            flex-shrink: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }

            @media screen and (max-width: 1200px) {
                width: 3.6rem;
                height: 3.6rem;
            }

            @media screen and (max-width: 800px) {
                width: 3.2rem;
                height: 3.2rem;
            }

            @media screen and (max-width: 600px) {
                width: 2.4rem;
                height: 2.4rem;
            }
        }

        &--item {
            width: fit-content;
            display: flex;
            align-items: center;
            justify-content: center;
            column-gap: var(--columnContent2);
            background: $color-second;
            padding: 1.8rem 4rem;
            border-radius: var(--radius);

            @media screen and (max-width: 1200px) {
                padding: 1.6rem 3rem;
                column-gap: var(--columnContent1);
            }

            @media screen and (max-width: 800px) {
                padding: 1.4rem 2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem 1rem;
            }

            @media screen and (max-width: 460px) {
                width: 100%;
                padding: 1rem;
            }

            .note-lg {
                color: $color-white;

                @media screen and (max-width: 600px) {
                    font-size: 1.4rem !important;
                }
            }
        }
    }

    &_content {
        display: flex;
        // align-items: center;
        // column-gap: var(--columnContent);

        @media screen and (max-width: 1024px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &_lf {
        width: calc(100% * 5 / 12);

        &--wrap {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 10rem 0;
            height: 100%;
            position: relative;
            .box-navi {
                position: absolute;
                bottom: 0;
                right: 0;

                @media screen and (max-width: 460px) {
                    right: 50%;
                    transform: translateX(50%);
                    width: fit-content;
                }
            }

            @media screen and (max-width: 1024px) {
                padding: 1.2rem 0 4rem;
            }
        }

        &--list {
            display: flex;
            flex-direction: column;

            row-gap: var(--rowContent1);

            max-height: 22rem;
            overflow-y: auto;

            @media screen and (max-width: 1024px) {
                max-height: none;
                overflow-y: auto;
            }
        }

        &--item {
            &:not(:last-child) {
                padding-bottom: var(--rowContent1);
                border-bottom: 0.1rem solid #d9d9d9;
            }

            &.actived {
                .note-lg {
                    color: $color-second !important;
                }
            }
            cursor: pointer;
            @include hover {
                .note-lg {
                    color: $color-second;
                }
            }

            .note-lg {
                transition: 0.3s linear;

                @media screen and (max-width: 460px) {
                    font-size: 1.4rem !important;
                }
            }
        }

        @media screen and (max-width: 1024px) {
            width: 100%;
        }
    }

    &_rt {
        flex: 1;

        &--wrap {
            width: calc(100% + ((100vw - 120rem) / 2));
            overflow: hidden;
            height: 100%;

            @media screen and (max-width: 1230px) {
                width: calc(100% + 3rem);
                margin-left: -1.5rem;
            }
        }

        &--list {
            height: 100%;
            display: flex;
            position: relative;

            .swiper {
                overflow: visible;
                margin-left: 0;
                width: 90%;

                @media screen and (max-width: 460px) {
                    width: 100%;
                }
            }

            .swiper-slide-active {
                // transform: translateX(calc(-100%));
                opacity: 1 !important;
                .why_rt--item-img::before {
                    clip-path: inset(0 0 0 100%);
                }

                .why_rt--item-content {
                    transform: translateX(0);
                    opacity: 1;
                }

                .why_rt--item-wrap {
                    margin-left: 0;
                }
            }

            .swiper-slide {
                height: auto;
                opacity: 0;
                width: 90%;

                @media screen and (max-width: 1024px) {
                    width: 100%;
                }
            }

            .swiper-slide {
                // transform: translateX(calc(-80%));
                opacity: 0;
            }

            .swiper-slide + .swiper-slide-next {
                opacity: 1;
            }

            .swiper-slide-next + .swiper-slide {
                opacity: 1;
            }

            .swiper-slide-prev {
                opacity: 0;
            }

            .swiper-slide + .swiper-slide-prev {
                opacity: 0;
            }

            // .swiper-slide-prev + .swiper-slide{
            //     transform: translateX(-70%);

            // }
        }

        &--item {
            height: 100%;
            width: 100%;
            position: relative;
            z-index: 3;
            transition: 1s ease;

            &-wrap {
                position: relative;
                height: 100%;
                // max-width: calc(100% - ((100vw - 120rem) / 2) + 3.2rem);
                transition: 0.5s linear;
                padding-left: 10%;

                @media screen and (max-width: 1024px) {
                    padding-left: 0;
                }
            }

            &-bg {
                // max-width: 59.6rem;
                width: 100%;
                margin-left: auto;
                height: 100%;
            }

            &-img {
                padding-top: calc(100% * 426 / 596);
                width: 100%;
                display: block;
                position: relative;
                overflow: hidden;
                height: 100%;

                @media screen and (max-width: 460px) {
                    padding-top: calc(100% * 360 / 460);
                }

                img {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    object-fit: cover;

                    @media screen and (max-width: 460px) {
                        object-position: center;
                    }
                }

                &::before {
                    content: "";
                    position: absolute;
                    inset: 0;
                    background: rgba($color: #000000, $alpha: 0.5);
                    width: 102%;
                    height: 100%;
                    z-index: 1;
                    clip-path: inset(0 0 0 0);
                    transition: 1s linear;
                }
            }

            &-content {
                display: flex;
                flex-direction: column;
                width: 50%;
                background: $color-white;
                padding: 1.6rem;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 3;
                max-height: 20rem;
                overflow-y: auto;
                transform: translateX(-100%);
                opacity: 0;
                transition: 1s linear;
                &::-webkit-scrollbar {
                    width: 0.2rem;
                }

                .mb-20 {
                    @media screen and (max-width: 600px) {
                        margin-bottom: 1rem;
                    }
                }

                @media screen and (max-width: 1400px) {
                    width: 100%;
                    max-width: 40rem;
                }

                @media screen and (max-width: 600px) {
                    width: 100%;
                    max-width: 100%;
                    max-height: 8rem;
                    padding: 1.2rem;
                    font-size: 1.4rem;
                }
            }
        }
    }
}

.should {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;
    &_wrap {
        display: flex;
        flex-direction: column;

        row-gap: var(--rowContent);

        @media screen and (max-width: 1200px) {
            row-gap: var(--rowContent2);
        }
    }

    &_content {
        max-width: 100rem;
        margin: 0 auto;
        width: 100%;
        display: flex;

        @media screen and (max-width: 1200px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &_lf {
        width: 60%;
        position: relative;
        flex-shrink: 0;
        z-index: 5;
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            max-width: 18rem;

            @media screen and (max-width: 1200px) {
                max-width: none;
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--columnContent2);
            }

            @media screen and (max-width: 460px) {
                flex-direction: column;
            }
        }

        &--item {
            border-bottom: 0.1rem solid transparent;
            cursor: pointer;
            transition: 0.3s linear;

            @media screen and (max-width: 1200px) {
                width: fit-content;
            }
            @media screen and (max-width: 460px) {
                width: 100%;
            }

            &.actived {
                border-color: $color-pri;

                .note-lg {
                    color: $color-third;
                }
            }

            @include hover {
                .note-lg {
                    color: $color-third;
                }
                border-color: $color-pri;
            }

            .note-lg {
                transition: 0.3s linear;
                font-weight: 500;

                @media screen and (max-width: 600px) {
                    font-size: 1.4rem;
                }
            }
        }

        &--cts {
        }

        &--ct {
            display: none;
            padding: 2.4rem;
            background: $color-white;
            &.showed {
                display: block;
                animation: anHienContent 0.8s linear;
            }

            ul {
                list-style: disc;
                margin-left: 2rem;
                li {
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem;
            }
        }

        @media screen and (max-width: 1200px) {
            width: 100%;
        }
    }

    &_rt {
        width: 100%;
        flex: 1;

        &--wrap {
            margin-left: calc(-100% - 0%);

            @media screen and (max-width: 1200px) {
                margin-left: 0;
            }
        }

        &--list {
            position: relative;
            z-index: 1;

            .swiper {
                overflow: visible;
                width: 100%;
                @media screen and (max-width: 1200px) {
                    overflow: hidden;
                }
            }

            .swiper-slide-active {
                opacity: 1 !important;
                .should_rt--item {
                    &::before {
                        opacity: 0 !important;

                        @media screen and (max-width: 1200px) {
                            display: none;
                        }
                    }
                }
            }

            .swiper-slide {
                overflow: visible;
                // transition: opacity 1s linear;
                width: 100% !important;
                opacity: 0;
                height: auto !important;
                // transition : 1s linear;

                &:first-child {
                    opacity: 1;
                    z-index: 2;
                }

                &:last-child {
                    opacity: 1;
                    z-index: 1;

                    @media screen and (max-width: 1200px) {
                        opacity: 0;
                    }

                    .should_rt--item {
                        &::before {
                            content: "";
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            bottom: -3.5%;
                            right: -1.8%;
                            border: 0.1rem solid $color-third;
                            opacity: 1;
                            transition: 0.3s linear;

                            @media screen and (max-width: 1200px) {
                                display: none;
                            }
                        }
                    }
                }

                @media screen and (max-width: 1200px) {
                    overflow: hidden;
                }
            }

            .swiper-slide + .swiper-slide-next {
                opacity: 1;
            }

            .swiper-slide-prev {
                opacity: 0 !important;
            }
        }

        &--item {
            position: relative;
            transition: 1s linear;
            height: 100%;
        }

        &--img {
            padding-top: calc(100% * 367 / 664);
            width: 100%;
            position: relative;
            display: block;
            overflow: hidden;

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
            }
        }
    }
}
