.banner-main {
    position: relative;
    margin-bottom: var(--size-pd);
    overflow: hidden;

    &-bg {
        width: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        z-index: -1;
        animation: bn 1.2s linear;
        @keyframes bn {
            0% {
                transform: scale(1.4);
                opacity: 0;
            }

            50% {
                transform: scale(1.2);
                opacity: 0.5;
            }

            100% {
                transform: scale(1);
                opacity: 1;
            }
        }

        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba($color: #000000, $alpha: 0.3);
            inset: 0;
        }

        img {
            width: 100% !important;
            height: 100% !important;
            object-fit: cover !important;
        }
    }

    &-wrap {
        width: 100%;
        min-height: 40rem;
        display: flex;
        // align-items: center;
        align-items: center;

        @media screen and (max-width: 1200px) {
            min-height: 35rem;
        }

        @media screen and (max-width: 800px) {
            min-height: 30rem;
        }

        @media screen and (max-width: 600px) {
            min-height: 25rem;
        }

        @media screen and (max-width: 460px) {
            min-height: 20rem;
        }
    }

    &-content {
        clip-path: inset(0 100% 0 0);

        @keyframes bnc {
            0% {
                clip-path: inset(0 100% 0 0);
            }
            50% {
                clip-path: inset(0 50% 0 0);
            }

            100% {
                clip-path: inset(0 0% 0 0);
            }
        }

        animation: bnc 1.2s 1.2s linear forwards;

        h1 {
            opacity: 0;
            width: 0;
            height: 0;
            visibility: hidden;
            overflow: hidden;
        }

        .breadcrumbs-wrap {
            padding-top: 0;
        }
    }
}

.banner_qc {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    overflow: hidden;

    &.ani {
        &.showed {
            .banner_qc--img {
                clip-path: inset(0 0 0 0);
            }
        }
    }

    &--wrap {
        h2 {
            display: none;
        }
    }

    &--img {
        padding-top: calc(100% * 431 / 1755);
        width: 100%;
        position: relative;
        display: block;
        overflow: hidden;
        clip-path: inset(0 100% 0 0);
        transition: 3s ease;

        @media screen and (max-width: 1200px) {
            clip-path: inset(0 0 0 0);
        }

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100% !important;
            height: 100% !important;
            object-fit: cover;
        }
    }
}

.banner_home {
    overflow: hidden;
    h1 {
        opacity: 0;
        width: 0;
        height: 0;
        visibility: hidden;
        overflow: hidden;
    }
    .container {
        @media screen and (max-width: 1200px) {
            padding: 0;
        }
    }
    &--wrap {
        display: flex;

        @media screen and (max-width: 1200px) {
            flex-direction: column-reverse;
        }
    }

    &--lf {
        width: calc(100% * 3 / 12 - 1.8rem);

        &-wrap {
            border: 0.1rem solid #eaeaea;
            border-right: 0;
            height: 100%;
            position: relative;
            margin-top: 0.6rem;
            border-top: none;
            z-index: 3000;


            
            &.activedIndex {
                z-index: 1000;
                border-color: transparent;
            }

            @media screen and (max-width: 1200px) {
                border: 0.1rem solid #eaeaea !important;
            }

            .menu-mega-lf-wrap {
                // background: red;
                display: block;
                height: 100%;
                opacity: 1 !important;
                pointer-events: visible !important;
                position: relative;
                z-index: 1000;
            }

            .menu-mega-lf-link {
                padding: 0.8rem 3rem 0.8rem 1rem;

                column-gap: 0.6rem;

                // @include hover {
                //     background: $color-brown2;
                //     .menu-mega-lf-ic {
                //         img {
                //             filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7500%)
                //                 hue-rotate(205deg) brightness(98%) contrast(104%);
                //         }
                //     }

                //     .note-md {
                //         color: $color-white;
                //     }
                // }

                @media screen and (max-width: 1200px) {
                    padding: 1rem;
                    column-gap: 0.6rem;
                }

                @media screen and (max-width: 600px) {
                    padding: 0.6rem 0.8rem;
                    // column-gap: 0.4rem;
                }

                .note-md {
                    @media screen and (max-width: 460px) {
                        @include fz-14;
                    }
                }
            }

            .menu-mega-lf-item {
                &:not(:last-child) {
                    border-bottom: 0.1rem solid #eaeaea;
                }

                @media screen and (max-width: 1200px) {
                    width: calc(100% / 4);
                    border-bottom: 0;
                }

                @media screen and (max-width: 800px) {
                    width: calc(100% / 3);
                    border-bottom: 0;
                }

                @media screen and (max-width: 600px) {
                    width: calc(100% / 2);
                    border-bottom: 0;
                }
            }

            .menu-mega-lf-ic {
                @media screen and (max-width: 1200px) {
                    margin-top: 0.25rem;
                }

                @media screen and (max-width: 600px) {
                    margin-top: 0;
                }
            }

            .menu-mega-lf-list {
                // max-height: 40rem;
                height: 100%;
                min-height: unset;

                @media screen and (max-width: 1200px) {
                    display: flex;
                    flex-wrap: wrap;
                    max-height: unset;
                }
            }
        }

        @media screen and (max-width: 1200px) {
            width: 100%;
            display: none;
        }
    }

    &--item {
        position: relative;
        min-height: 40rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;

        @media screen and (max-width: 800px) {
            min-height: 20rem;
        }

        @media screen and (max-width: 600px) {
            min-height: 10rem;
        }
    }

    &--bg {
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover !important;
        }

        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            inset: 0;
            background: #1c274c99;
        }
    }

    &--box {
        position: relative;
        z-index: 2;
        display: flex;
        padding: 10rem 0 10rem 7.5rem;
        column-gap: var(--columnContent1);
        justify-content: space-between;

        @media screen and (max-width: 800px) {
            padding: 5rem 0 5rem 4rem;
        }

        @media screen and (max-width: 700px) {
            padding: 5rem 0 5rem 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 4rem 0 4rem 1.2rem;
        }
    }

    &--img {
        flex: 1;
        width: 100%;
        max-width: 26rem;
        border-radius: var(--radius);
        overflow: hidden;
        &-wrap {
            padding-top: calc(100% * 242 / 260);
            width: 100%;
            position: relative;
            display: block;
            overflow: hidden;
            transform: translateX(100%);
            opacity: 0;
            transition: 1s ease;

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100% !important;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        width: 55%;
        .title-main {
            text-align: start;
            color: $color-white;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }

        .note-text {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }

        .btn {
            margin-top: var(--rowContent1);
        }
    }

    &--rt {
        flex: 1;
        width: 100%;
        &-wrap {
            display: flex;
            gap: 0.6rem;
            padding: 0.6rem 0 0.6rem 0.6rem;

            @media screen and (max-width: 1200px) {
                padding: 0;
                
            }

            @media screen and (max-width: 800px) {
               gap: 0.3rem;
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }

        &-lf {
            width: 72%;

            @media screen and (max-width: 800px) {
                width: 65%;
            }

            @media screen and (max-width: 700px) {
                width: 60%;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }

            &-wrap {
                height: 100%;
                position: relative;

                .swiper {
                    height: 100%;
                    border-radius: var(--radius);
                    overflow: hidden;

                    @media screen and (max-width: 1200px) {
                        border-radius: 0;
                    }
                }

                .swiper-slide {
                    height: auto;
                }

                .swiper-slide-active {
                    .banner_home--img-wrap {
                        transform: translateX(0);
                        opacity: 1;
                    }
                }
                .swiper-pagination {
                    position: absolute;
                    bottom: 2rem;
                    left: 50%;
                    margin-top: 0;
                    transform: translateX(-50%);

                    @media screen and (max-width: 600px) {
                        bottom: 1rem;
                    }
                }
            }
        }

        &-img {
            width: 100%;
            position: relative;
            overflow: hidden;
            padding-top: calc(100% * 237 / 280);
            display: block;
            height: 100%;
            border-radius: var(--radius);
            overflow: hidden;

            @media screen and (max-width: 1200px) {
                border-radius: 0;
            }
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-item {
            width: 100%;
            height: 50%;

            @media screen and (max-width: 600px) {
                width: calc(100% / 2);
            }
        }

        &-rt {
            width: 100%;
            flex: 1;

            &-wrap {
                display: flex;
                flex-direction: column;
                height: 100%;
                gap: 0.6rem;

                @media screen and (max-width: 800px) {
                    gap: 0.3rem;
                }

                @media screen and (max-width: 600px) {
                    flex-direction: row;
                }
            }
        }
    }
}
