.footer {
  padding-top: 6rem;
  background: $color-white;
  overflow: hidden;
  position: relative;
  @media screen and (max-width: 1200px) {
    padding-top: 3rem;
    z-index: 1200;
  }

  @media screen and (max-width: 800px) {
    padding-top: 2rem;
  }

  &-mxh {
    &.other{
      .footer-mxh-list {
        column-gap: 0.8rem;

        @media screen and (max-width: 800px) {
          column-gap: 0.6rem;
        }

        @media screen and (max-width: 600px) {
          column-gap: 0.4rem;
        }
    }

    .footer-mxh-link {
        width: 3.2rem;
        height: 3.2rem;
        border-radius: 50%;
        overflow: hidden;

        img {
            transition: none;
        }

        @include hover {
            img {
                transform: none;
                filter: brightness(0) saturate(100%) invert(28%) sepia(74%) saturate(960%) hue-rotate(354deg)
                    brightness(93%) contrast(93%);
            }
        }

        @media screen and (max-width: 800px) {
          width: 2.8rem;
          height: 2.8rem;
        }


        @media screen and (max-width: 600px) {
          width: 2.4rem;
          height: 2.4rem;
        }
    }
    }
    &-list {
      display: flex;
      align-items: center;
      column-gap: 2.4rem;

      @media screen and (max-width: 1200px) {
        column-gap: 1.2rem;
      }
    }

    &-item {
    }

    &-link {
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;

      @include hover {
        img {
          transform: scale(1.3);
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        transition: 0.5s ease;
      }

      @media screen and (max-width: 600px) {
        width: 2rem;
        height: 2rem;
      }
    }
  }

  &-info {
    &-list {
      display: flex;
      flex-direction: column;
      row-gap: 0.5rem;
    }

    &-item {
      .note-sm {
        @media screen and (max-width: 600px) {
          @include fz-12;
        }
      }
    }
  }

  &-wrap {
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent2);

    @media screen and (max-width: 600px) {
      row-gap: var(--rowContent1);
    }
  }

  &-title {
  @include fz-16;
    font-weight: 700;
    color: $color-text4;
  }

  .menu-list {
    flex-direction: column;
    row-gap: 1rem;
    margin: 0;

    @media screen and (max-width: 800px) {
      flex-direction: row;
      column-gap: 1.2rem;
      flex-wrap: wrap;
    }
    .menu-item {
      height: fit-content;
      @include fz-14;
      &.current-menu-item {
        .menu-link {
          font-weight: 600;
        }
      }

      @media screen and (max-width: 800px) {
        width: calc(50% - 0.6rem);
      }

      @media screen and (max-width: 600px) {
        width: calc((100% / 2) - 0.6rem);
      }

      @media screen and (max-width: 460px) {
        width: 100%;
      }

      @media screen and (max-width: 360px) {
        width: calc((100% / 2) - 0.6rem);
      }

      .menu-link {
        @include fz-14;
        padding: 0;
        height: unset;
        white-space: pre-wrap;
        @media screen and (max-width: 800px) {
          width: fit-content;
        }

        @media screen and (max-width: 600px) {
          @include fz-12;
        }

        &:before {
          display: none;
        }
      }
    }
  }

  &-pay {
    &-list {
      display: flex;
      align-items: center;
      gap: 1rem;
      flex-wrap: wrap;

      @media screen and (max-width: 1200px) {
        column-gap: 0.5rem;
        row-gap: 1rem;
      }
    }

    &-item {
      width: calc(100% / 6 - 1rem);
      flex-shrink: 0;

      @media screen and (max-width: 1200px) {
        width: calc(100% / 4 - 0.5rem);
      }

      @media screen and (max-width: 800px) {
        width: calc(100% / 6 - 0.5rem);
      }

      @media screen and (max-width: 600px) {
        width: fit-content;
      }
    }

    &-img {
      width: 100%;
      height: 2.4rem;

      @include hover {
        img {
          transform: scale(1.4);
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.5s ease;
      }
    }
  }

  &-top {
    padding-bottom: var(--rowContent2);
    border-bottom: 0.1rem solid #e1e1e1;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: -9rem;
      transform: translateY(-50%);
      background-image: url(../assets/images/fttt1.png);
      width: 24rem;
      height: 24rem;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      pointer-events: none;
      @media screen and (max-width:1400px) {
        display: none;
      }
    }
    @media screen and (max-width: 800px) {
      padding-top: var(--rowContent2);
      border-top: 0.1rem solid #e1e1e1;
      border-bottom: none;
    }

    &-wrap {
    }

    &-list {
      flex-wrap: nowrap !important;
      row-gap: var(--rowContent2);

      @media screen and (max-width: 800px) {
        flex-wrap: wrap !important;
      }
    }

    &-item {
      @media screen and (max-width: 800px) {
        width: calc(100% / 2) !important;

        &:first-child {
          order: 3;
        }

        &:nth-child(2) {
          order: 1;
        }

        &:nth-child(3) {
          order: 2;
        }

        &:last-child {
          order: 4;
        }
      }

      @media screen and (max-width: 600px) {
        width: 50% !important;
      }

      @media screen and (max-width: 360px) {
        width: 100% !important;
      }

      &-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        row-gap: var(--rowContent2);
        height: 100%;
      }

      &-top {
        display: flex;
        flex-direction: column;
        row-gap: 1rem;
      }

      &-bottom {
        display: flex;
        flex-direction: column;
        row-gap: 1rem;

        .note-sm {
          color: #5d5c5c;
        }
      }
    }
  }

  &-logo {
    max-width: 15.4rem;
    display: block;
    margin: auto;
    width: 100%;

    @media screen and (max-width: 1200px) {
      max-width: 13rem;
    }

    @media screen and (max-width: 800px) {
      max-width: 10rem;
      margin: unset;
    }

    @media screen and (max-width: 600px) {
      max-width: 8rem;
    }

    @media screen and (max-width: 460px) {
      max-width: 6rem;
    }

    @include hover {
      img {
        transform: scale(0.9);
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: 0.5s ease;
    }
  }

  &-center {
    @media screen and (max-width: 800px) {
      order: -1;
    }

    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background-image: url(../assets/images/fttt2.png);
      width: 24rem;
      height: 24rem;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      pointer-events: none;

      @media screen and (max-width:1400px) {
        display: none;
      }
    }

    &-wrap {
    }

    &-list {
      justify-content: space-between;
      row-gap: var(--rowContent2);
    }

    &-item {
      width: calc(100% * 3 / 12);

      @media screen and (max-width: 1200px) {
        width: calc(100% * 4 / 12);
      }

      @media screen and (max-width: 800px) {
        width: 50%;

        &:nth-child(2) {
          width: 100%;
          order: -1;
        }
      }

      @media screen and (max-width: 600px) {
        width: 100% !important;
      }

      &-wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
        justify-content: space-between;
        height: 100%;

        @media screen and (max-width: 600px) {
          row-gap: var(--rowContent1);
        }
      }

      &:nth-child(2) {
        .footer-center-wrap {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
      }
    }

    &-img {
      max-width: 15.8rem;
      width: 100%;

      @media screen and (max-width: 1200px) {
        max-width: 14rem;
      }

      @media screen and (max-width: 800px) {
        max-width: 13rem;
      }

      @media screen and (max-width: 600px) {
        max-width: 12rem;
      }

      @media screen and (max-width: 460px) {
        max-width: 10rem;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    .list-address {
      row-gap: 1rem;
      // padding: 0 0.5rem;
      display: flex;
      flex-direction: column;
    }

    .ic-address {
      border: none !important;
      border-radius: unset !important;
      width: 1.8rem;
      // height: 1.8rem;
      height: 100%;
      flex-shrink: 0;
      // margin-top: -0.3rem;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      top: -0.1rem;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        filter: none;
      }
    }

    .item-address {
      // align-items: center !important;
      column-gap: 0.8rem;
      flex-direction: row;
      display: flex;
      @include hover {
        .link-address {
          color: $color-pri;
        }

        .ic-address {
          filter: brightness(0) saturate(100%) invert(65%) sepia(95%) saturate(4265%) hue-rotate(357deg) brightness(96%)
            contrast(107%);
        }
      }
    }

    .link-address {
      color: $color-text4;
      font-weight: 600;
      font-size: 1.4rem;
      transition: none;

      @media screen and (max-width: 600px) {
        @include fz-12;
      }

      @include hover {
        color: $color-pri;
      }
    }
  }

  &-bottom {
    border-top: 0.1rem solid #e1e1e1;
    padding: 1.6rem 0;

    @media screen and (max-width: 1024px) {
      padding: 1.2rem 0;
    }
    &-wrap {
    }

    &-content {
      display: flex;
      align-items: center;
      column-gap: 6rem;
      width: fit-content;
      margin: 0 auto;

      @media screen and (max-width: 1200px) {
        column-gap: 3rem;
      }

      @media screen and (max-width: 1024px) {
        flex-direction: column;
        row-gap: 0.6rem;
      }

      .note-sm {
        position: relative;
        text-align: center;
        &:not(:first-child) {
          &::before {
            content: "|";
            position: absolute;
            left: -3rem;
            top: 50%;
            transform: translateY(-50%);
            color: #d9d9d9;

            @media screen and (max-width: 1200px) {
              left: -1.5rem;
            }

            @media screen and (max-width: 1024px) {
              display: none;
            }
          }
        }

        @media screen and (max-width: 600px) {
          @include fz-12;
        }
      }
    }
  }
}
