.form {
    &-list {
        row-gap: var(--rowContent2);
    }

    &-item {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }
        &-ip {
            height: 4.4rem;
            display: flex;
            align-items: center;
            padding: 1rem 2rem;
            background: $color-white;
            color: $color-black;
            border-radius: var(--radius);
            border: 0.1rem solid $color-gray8;
            transition: 0.5s linear;
            width: 100%;
            &::placeholder {
                color: $color-gray7;
            }

            &:focus {
                border: 0.1rem solid $color-pri;
            }

            @media screen and (max-width: 1200px) {
                padding: 1rem 1.2rem;
                height: 4rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem 1.2rem;
                height: 3.6rem;
            }
        }

        &-lb {
            font-weight: 600;
            color: $color-black;
            @include fz-14;
        }

        &-tx {
            min-height: 10rem;
            display: flex;
            align-items: center;
            padding: 1rem 2rem;
            background: $color-white;
            color: $color-black;
            border-radius: var(--radius);
            border: 0.1rem solid $color-gray8;
            transition: 0.5s linear;
            width: 100%;
            &::placeholder {
                color: $color-gray7;
            }

            &:focus {
                border: 0.1rem solid $color-pri;
            }

            @media screen and (max-width: 1200px) {
                padding: 1rem 1.2rem;
                min-height: 14rem !important;
            }
        }
    }
}

.contact {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        // align-items: center;

        @media screen and (max-width:600px) {
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &_lf {

        @media screen and (max-width:600px) {
           width: 100%;
        }

        &--wrap {
            max-width: 54rem;
            width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &--top {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--center {
            max-width: 40rem;
            width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .list-address {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);
            }

            .item-address {
                display: inline-block;
            }
        }

        &--bottom {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }
    }

    &_rt {
        @media screen and (max-width:600px) {
            width: 100%;
         }
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            .btn {
                margin: 0 auto;
                min-width: unset;
            }

            form {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);
            }

            .form-list {
                row-gap: var(--rowContent1);

                .form-item-lb {
                    @include fz-16;
                }
            }

            .form-item {
                &:first-child {
                    width: 100%;
                }

                width: 50%;

                &:last-child {
                    width: 100%;
                }

                @media screen and (max-width:800px) {
                    width: 100% !important;
                }
            }

            .form-item-tx {
                min-height: 18rem;
            }
        }
    }
}

.contact_map {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        padding-top: calc(100% * 600 / 1728);
        position: relative;
        overflow: hidden;
        width: 100%;
        display: block;

        @media screen and (max-width:600px) {
            padding-top: 100%;
        }

        iframe {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
