.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3002;
  transition: 0.5s ease;
  // border-bottom: 0.1rem solid #e0e0e0;
  // overflow: hidden;
  background-color: $color-white;
  // border-color: rgba($color: $color-pri, $alpha: 0.2);
  box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.2);
  @media screen and (max-width: 1200px) {
    border-bottom: none;
  }

  &-lang {
    &-list {
      display: flex;
      align-items: center;
      column-gap: 1.2rem;
    }

    &-item {
      position: relative;

      &:not(:first-child) {
        &:before {
          content: "|";
          position: absolute;
          font-size: 1.6rem !important;
          top: 50%;
          transform: translateY(-50%);
          color: $color-white;
          left: -0.8rem;

          @media screen and (max-width: 1200px) {
            font-size: 2rem !important;
            top: 45%;
          }

          @media screen and (max-width: 1200px) {
            font-size: 1.6rem !important;
          }
        }
      }

      &.actived {
        .header-lang-link {
          color: $color-pri !important;
          font-weight: 700;
        }
      }
    }

    &-link {
      color: $color-white;
      font-size: 1.6rem !important;
      display: block;
      text-transform: uppercase;
      line-height: 100%;
      font-weight: 600;
      @include hover {
        color: $color-pri;
      }

      @media screen and (max-width: 1200px) {
        font-size: 2rem !important;
      }

      @media screen and (max-width: 1200px) {
        font-size: 1.6rem !important;
      }
    }
  }

  &-mega {
    &-btn {
      background: transparent;
      padding: 0.6rem 1rem;
      border-radius: var(--radius);
      display: flex;
      align-items: center;
      column-gap: 0.8rem;
      cursor: pointer;
      position: relative;
      user-select: none;
      z-index: 20;
      transition: 0.3s linear;
      border: 0.15rem solid $color-white;
      .note-md {
        font-weight: 600;
        color: $color-white !important;
      }

      // @include hover {
      //   background: #eae6e6;
      //   .header-mega-btn-ic {
      //     .line {
      //       &:first-child {
      //         width: 60%;
      //       }

      //       &:nth-child(2) {
      //         width: 80%;
      //       }

      //       &:last-child {
      //         width: 100%;
      //       }
      //     }
      //   }
      // }

      &-ic {
        width: 2.4rem;
        height: 1.4rem;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        .line {
          height: 0.2rem;
          border-radius: 1rem;
          width: 100%;
          background: $color-white;
          position: absolute;
          transition: 0.3s linear;
          &:first-child {
            width: 100%;
            top: 0;
            left: 0;
          }

          &:nth-child(2) {
            width: 80%;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
          }

          &:last-child {
            width: 60%;
            bottom: 0;
            left: 0;
          }
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    &-menu {
      width: 121.6rem;
      position: fixed;
      top:calc(var(--size-hd) + 0.6rem);
      left: 50%;
      transform: translateX(-50%);
      z-index: 1100;
      transition: 0.1s ease;
      pointer-events: none;
      visibility: hidden;

      opacity: 0;

      &.normal{
        &.showed {
          opacity: 1;
          visibility: visible;
          pointer-events: visible;
          & ~ .menu-mega-modal {
            opacity: 1;
            visibility: visible;
            pointer-events: visible;
          }
        }


        // &.showed2 {
        //   opacity: 1;
        //   visibility: visible;
        //   pointer-events: visible;
        //   & ~ .menu-mega-modal {
        //     opacity: 1;
        //     visibility: visible;
        //     pointer-events: visible;
            
        //   }
        //   .menu-mega-lf-wrap {
        //     pointer-events: visible;
        //     opacity: 1;
        //   }
  
        //   .menu-mega-lf {
        //     pointer-events: visible;
        //     opacity: 1;
        //   }
        // }
        &.showed2 {
          opacity: 1;
          visibility: visible;
          pointer-events: visible;
          & ~ .menu-mega-modal {
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            
          }
          .menu-mega-lf-wrap {
            pointer-events: none;
            opacity: 0;
          }
  
          .menu-mega-lf {
            pointer-events: none;
            opacity: 0;
          }
        }

      }




      


      &.showed2 {
        opacity: 1;
        visibility: visible;
        pointer-events: visible;
        & ~ .menu-mega-modal {
          opacity: 0;
          visibility: hidden;
          pointer-events: none;
          
        }
        .menu-mega-lf-wrap {
          pointer-events: none;
          opacity: 0;
        }

        .menu-mega-lf {
          pointer-events: none;
          opacity: 0;
        }
      }

      // &.showed2{
      //   opacity: 1;
      //   visibility: visible;
      //   pointer-events: visible;
      //   & ~ .menu-mega-modal {
      //     opacity: 0;
      //   }

      //   .menu-mega-lf-wrap{
      //     display: none;
      //   }
      // }

      @media screen and (max-width: 1300px) {
        width: 121.5rem;
      }

      @media screen and (max-width: 1230px) {
        width: 119.5rem;
      }

      @media screen and (max-width: 1220px) {
        width: 118.8rem;
      }

      @media screen and (max-width: 1210px) {
        width: 117rem;
      }
    }
  }

  &-search {
    width: 100%;
    max-width: 46rem;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    &-wrap {
      width: 100%;
      position: relative;
      display: flex;
      align-items: center;
      transition: 0.3s linear;

      input {
        width: 100%;
        height: 4.4rem;
        padding: 1rem 4rem 1rem 2rem;
        background: #6872c4;
        border-radius: 0.8rem;
        display: block;
        color: $color-white;
        &::placeholder {
          color: #dcdef5;
        }

        @media screen and (max-width: 1200px) {
          background: $color-white;
          box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.5);
          color: $color-black;
          border-radius: 0.4rem;
          padding: 1rem 4rem 1rem 1.2rem;
          &::placeholder {
            color: $color-black;
          }
        }
      }

      @media screen and (max-width: 1200px) {
        position: absolute;
        top: calc(100% + 0.5rem);
        right: -4.6rem;
        min-width: 26rem;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;

        &.showed {
          opacity: 1;
          visibility: visible;
          pointer-events: visible;
        }
      }
    }

    &-btn {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 1.6rem;
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      align-items: center;
      justify-content: center;

      @media screen and (max-width: 1200px) {
        right: 1.2rem;
      }

      @include hover {
        img {
          filter: brightness(0) saturate(100%) invert(41%) sepia(57%) saturate(1862%) hue-rotate(3deg) brightness(100%)
            contrast(102%);
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;

        @media screen and (max-width: 1200px) {
          filter: brightness(0) saturate(100%) invert(0%) sepia(3%) saturate(7489%) hue-rotate(54deg) brightness(89%)
            contrast(98%);
        }
      }
    }

    &-mb {
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      cursor: pointer;
      animation: anHienBtn 0.8s ease;
      display: none;

      @keyframes anHienBtn {
        0% {
          opacity: 0;
        }
        100% {
          opacity: 1;
        }
      }

      &.btn-search {
        @media screen and (max-width: 1200px) {
          display: flex;
        }
      }

      &.btn-search-exit {
        display: none;
      }

      &.hiddened {
        display: none;
      }

      &.showed {
        display: flex;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        filter: brightness(0) saturate(100%) invert(44%) sepia(98%) saturate(1566%) hue-rotate(359deg) brightness(97%)
          contrast(106%);
      }
    }

    @media screen and (max-width: 1200px) {
      max-width: none;
      width: fit-content;
    }
  }

  &-card {
    width: 40rem;
    position: fixed;
    background: $color-white;
    top: calc(var(--size-hd) - 6rem);
    right: calc((100vw - 121.7rem) / 2);
    z-index: 2005;
    box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);
    transform: translateY(20%);
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: 0.5s ease;
    display: none;
    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 4rem;
      background: transparent;
      top: -3rem;
      left: 0;
    }
    &-modal {
      position: fixed;
      inset: 0;
      width: 100vw;
      height: 100vh;
      z-index: 2003;
      // height: calc(100% - var(--size-hd));
      background: rgba($color: #000000, $alpha: 0.5);
      display: none;
      visibility: hidden;
      pointer-events: none;
      opacity: 0;
      transition: 0.3s linear;
      display: none;
      @media screen and (max-width: 1200px) {
        display: block;
      }
    }

    &.actived {
      opacity: 1;
      pointer-events: visible;
      transform: translateY(0%);
      visibility: visible;

      @media screen and (max-width: 1200px) {
        transform: translate(0%, 0%);

        & ~ .header-card-modal {
          opacity: 1;
          visibility: visible;
          pointer-events: visible;
        }
      }
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }

    @media screen and (max-width: 1200px) {
      position: fixed;
      top: 0;
      right: 0;
      height: 100%;
      transform: translate(100%, 0%);
      width: 35rem;
      &::before {
        display: none;
      }
    }

    &-exits {
      position: absolute;
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 1rem;
      right: 1rem;
      cursor: pointer;
      display: none;
      @media screen and (max-width: 1200px) {
        display: flex;
      }
    }

    &-have {
      // display: none;
      .cart_content--total {
        display: none;
      }

      &-btn {
        display: flex;
        padding: 1.2rem;
        column-gap: 0.8rem;
        justify-content: space-between;
        .btn {
          width: calc(50% - 0.8rem);
          border-radius: 0;
        }
      }

      .cart_content--img {
        max-width: 5rem;
      }

      .cart_content--info {
        width: 100%;
      }

      // .cart_content--info{
      //   display: flex;
      //   flex-direction: column;
      //   row-gap: var(--rowContent0);
      // }

      .cart_content--list {
        row-gap: var(--rowContent0);
        max-height: 30vh;
      }

      .cart_content--item {
        padding: 0;
        flex-wrap: wrap;
        position: relative;
        justify-content: space-between !important;
        width: 100%;
        &:not(:last-child) {
          padding-bottom: 0.8rem;
          border-bottom: 0.1rem solid rgba($color: #000000, $alpha: 0.1);
        }
        &::before {
          display: none;
        }
      }

      .cart_content--quantity {
        width: fit-content;
      }

      .cart_content--price {
        width: fit-content;
      }

      .cart_content--ex {
        position: absolute;
        top: 0rem;
        right: 0;
        width: fit-content;
        .cart_content--ic {
        }
      }
    }

    &-none {
      width: 100%;
      padding-bottom: 1.6rem;
      display: none;
      @media screen and (max-width: 1200px) {
        padding-bottom: 0;
      }
      &-img {
        width: 100%;
        // height: 100%;
        max-width: 40%;
        margin: 0 auto;

        @media screen and (max-width: 1200px) {
          max-width: 40%;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .note-text {
        @include fz-24;
        color: $color-black;
        font-weight: 600;
        text-align: center;

        margin-bottom: 1rem;
      }

      .btn {
        margin: 0 auto;
      }
    }
  }

  &-cart {
    &-mb {
      width: 2.6rem;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      cursor: pointer;
      position: relative;
      display: none;

      @media screen and (max-width: 1200px) {
        display: flex;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }

      .num {
        position: absolute;
        top: 1.6rem;
        right: -0.5rem;
        width: 1.4rem;
        height: 1.4rem;
        border-radius: 50%;
        background: $color-white;
        display: flex;
        justify-content: center;
        align-items: center;
        @include fz-12;
        color: $color-pri;
        @media screen and (max-width: 460px) {
          top: 1rem;
        }
      }
    }
  }

  &-top {
    position: relative;
    transition: all 0.5s ease;
    height: 4rem;
    background: $color-white;
    @media screen and (max-width: 1200px) {
      height: 3.2rem;
    }

    @media screen and (max-width: 460px) {
      height: 2.4rem;
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      padding: 1rem 0;
    }

    &-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      column-gap: 1.2rem;
      transition: 0.5s ease;
      position: relative;
      opacity: 1;
      pointer-events: none;

      @media screen and (max-width: 1200px) {
        opacity: 1;
      }

      @keyframes chuChay {
        0% {
          transform: translateX(100%);
        }

        // 50% {
        //   transform: translateX(-100%);
        // }

        100% {
          transform: translateX(-100%);
        }
      }

      animation: chuChay 20s linear infinite;
      // @media screen and (max-width: 1024px) {

      // }
    }

    &-item {
      display: flex;
      column-gap: 1rem;
      align-items: center;
      // width: calc(100% / 3);
      .note-sm {
        color: $color-text3;
        font-weight: 600;
        white-space: nowrap;
        // display: -webkit-box;
        // -webkit-line-clamp: 1;
        // -webkit-box-orient: vertical;
        // overflow: hidden;
        display: flex;
        column-gap: 1rem;
        align-items: center;

        p {
          white-space: nowrap;
          margin: 0;
          display: flex;
          align-items: center;
          column-gap: 0.5rem;
          @include fz-16;
          @media screen and (max-width: 1200px) {
            @include fz-14;
          }

          @media screen and (max-width: 460px) {
            @include fz-12;
          }
        }

        strong {
          @include fz-16;
          color: $color-blue;
          font-weight: 700;
          // white-space: nowrap;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          @media screen and (max-width: 1200px) {
            @include fz-14;
          }

          @media screen and (max-width: 460px) {
            @include fz-12;
          }
        }

        @media screen and (max-width: 460px) {
          @include fz-12;
        }
      }

      @media screen and (max-width: 1200px) {
        column-gap: 0.5rem;
      }

      @media screen and (max-width: 460px) {
        column-gap: 0.25rem;
      }
    }

    &-ic {
      width: 2rem;
      height: 2rem;
      flex-shrink: 0;
      position: relative;
      // top: 0.3rem;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      @media screen and (max-width: 1200px) {
        top: 0.1rem;
      }

      @media screen and (max-width: 460px) {
        top: 0rem;
        width: 1.8rem;
        height: 1.8rem;
      }
    }

    &-mb {
      height: 6rem;
      width: 100%;
      display: none;
      position: relative;

      @media screen and (max-width: 600px) {
        height: 5rem;
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -1.5rem;
        right: -1.5rem;
        width: calc(100% + 3rem);
        height: 100%;
        background: $color-pri;
      }

      @media screen and (max-width: 1200px) {
        display: block;
      }
      &-wrap {
        height: 100%;
        display: flex;
        justify-content: space-between;

        .header-logo {
          img {
            filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(261deg)
              brightness(110%) contrast(101%);
          }
        }

        .btn {
          background: $color-white;
          &-text {
            color: $color-pri;
          }
        }

        .header-login {
          .header-user {
            &::before {
              filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(261deg)
                brightness(110%) contrast(101%);
            }

            .admin_info--if {
              .note-text {
                color: $color-white;
              }
              .note-sm {
                color: $color-white;
              }
            }
          }
        }
      }
    }
  }

  &-center {
    height: 10rem;
    background: $color-second;
    transition: 0.5s ease;
    @media screen and (max-width: 1200px) {
      height: 7rem;
    }

    @media screen and (max-width: 600px) {
      height: 6rem;
    }

    @media screen and (max-width: 460px) {
      height: 5rem;
    }

    &-box {
      display: flex;
      //  align-items: center;
      column-gap: 2.4rem;
      width: 76%;
      justify-content: space-between;

      @media screen and (max-width: 1230px) {
        width: 78%;
      }

      @media screen and (max-width: 1200px) {
        width: fit-content;
        column-gap: 1.2rem;
      }
    }

    &-list {
      display: flex;
      align-items: center;
      column-gap: 1.2rem;

      @media screen and (max-width: 1200px) {
        // display: none;
        order: -1;
      }
    }

    .header-bottom-lf {
      @media screen and (max-width: 1200px) {
        display: none;
      }
    }

    &-item {
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;

      @media screen and (max-width: 1200px) {
        display: none;
      }

      &.lang {
        display: flex;
      }

      &:not(:last-child) {
        padding-right: 2rem;
        &::before {
          content: "";
          position: absolute;
          right: 0;
          width: 0.1rem;
          height: 30%;
          top: 50%;
          transform: translateY(-50%);
          background: $color-four;
        }
      }

      @include hover {
        .header-center-ic img {
          filter: brightness(0) saturate(100%) invert(41%) sepia(57%) saturate(1862%) hue-rotate(3deg) brightness(100%)
            contrast(102%);
        }
        .header-center-link {
          color: $color-pri;
        }
      }

      &.cart-hd {
        position: relative;

        // @include hover {
        //   .header-card {
        //     opacity: 1;
        //     pointer-events: visible;
        //     visibility: visible;
        //     transform: translateY(0);
        //   }
        // }
      }
    }

    &-link {
      display: flex;
      align-items: center;
      column-gap: 0.8rem;
      color: $color-white;
      font-weight: 600;
      transition: none;
    }

    &-ic {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3rem;
      height: 3rem;
      flex-shrink: 0;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-wrap {
      height: 100%;
      display: flex;
      // align-items: center;
      // padding: 2rem 0;
      justify-content: space-between;
      column-gap: 2rem;
    }
  }

  &-bottom {
    height: 6rem;
    transition: all 0.5s ease;

    @media screen and (max-width: 1200px) {
      height: 6rem;
      display: none;
    }

    @media screen and (max-width: 600px) {
      height: 5rem;
    }

    &-wrap {
      display: flex;

      column-gap: 4.8rem;
      height: 100%;
      justify-content: space-between;
      width: 100%;

      transition: all 0.3s ease;

      @media screen and (max-width: 1200px) {
        .header-logo {
          display: none;
        }
      }

      .header-box {
        @media screen and (max-width: 1200px) {
          .header-login {
            display: none;
          }
        }
      }
    }

    &-lf {
      // max-width: calc(100% * 3 / 12 - 1.6rem);
      // width: 100%;
      display: flex;
      align-items: center;
      &-wrap {
        width: 100%;
        // height: 100%;
        position: relative;
        display: flex;
        align-items: center;

        &::before{
          content: "";
          width: 100%;
          height: 6rem;
          position: absolute;
          bottom: -100%;
          background: transparent;
          pointer-events: none;
        }

        &.actived {
          &::before{
            pointer-events: visible;
          }

          // .header-mega-btn {
          //   // background: $color-pri;

          //   // .note-md {
          //   //   color: $color-white;
          //   // }

          //   .header-mega-btn-ic {
          //     .line {
          //       // background: $color-white;
          //       &:first-child {
          //         width: 60%;
          //       }

          //       &:nth-child(2) {
          //         width: 80%;
          //       }

          //       &:last-child {
          //         width: 100%;
          //       }
          //     }
          //   }
          // }

          // .header-mega-menu {
          //   transform: translateY(0%);
          //   opacity: 1;
          //   pointer-events: visible;
          //   visibility: visible;
          // }

          .menu-mega-modal {
            opacity: 1;
            pointer-events: visible;
            visibility: visible;
          }
        }

        &.actived2 {
          &::before{
            pointer-events: visible;
          }
          // .header-mega-menu {
          //   transform: translateY(0%);
          //   opacity: 1;
          //   pointer-events: visible;
          //   visibility: visible;
          // }

         

          // .header-mega-menu{
          //   width: 93rem !important;
          //   left: unset;
          //   left: calc(100% + 3.7rem);
          // }

          .menu-mega-modal {
            opacity: 0;
            pointer-events: none;
            visibility: hidden;
          }
        }
      }

      @media screen and (max-width: 1300px) {
        max-width: calc(100% * 3 / 12 - 1.8rem);
      }
    }

    &-rt {
      flex: 1;
      width: 100%;

      &-wrap {
        height: 100%;
      }
    }
  }

  // @media screen and (max-width:1200px) {
  //   height: 7rem;
  // }

  &.actived {
    background: $color-white;
    height: auto;
    box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.2);
    border-color: transparent;
    z-index: 5000;
    & ~ .header-mega-menu {
      top: 11.1rem;
    }

    .header-bottom-lf-wrap{
      &::before{
        height: 4rem;
        bottom: -50%;
      }
    }

    .header-logo {
      transform: scale(0.9);
    }

    .header-top {
      height: 3.5rem;

      // &-list {
      //   opacity: 0;
      // }
    }

    .header-center {
      height: 7rem;
    }

    .header-bottom {
      height: 5rem;
    }

    .menu-list {
      .menu-item {
        .menu-link {
          &::before {
            bottom: 0%;
          }
        }
      }
    }
  }

  &-btn {
    display: flex;
    height: 100%;
    align-items: center;
  }

  &-nav {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.2rem;
    height: 100%;
    @media screen and (max-width: 1200px) {
      .menu-list {
        display: none;
      }
    }
  }

  &-logo {
    max-width: 6.1rem;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
    transition: all 0.5s ease;

    .icon {
      width: 100%;
      height: 100%;
    }

    @include hover {
      img {
        transform: scale(0.9);
      }
    }

    img {
      width: 100%;
      height: 100%;
      transition: all 0.3s ease;
      object-fit: contain;
    }

    @media screen and (max-width: 1200px) {
      max-width: 4.8rem;
    }

    @media screen and (max-width: 600px) {
      max-width: 4.2rem;
    }

    @media screen and (max-width: 460px) {
      max-width: 3.8rem;
    }
  }

  &-mobi {
    width: 3.4rem;
    height: 2.4rem;
    position: relative;
    display: none;
    cursor: pointer;
    margin: auto 0;
    flex-shrink: 0;
    @media screen and (max-width: 1200px) {
      display: block;
    }

    @media screen and (max-width: 600px) {
      height: 2.2rem;
      width: 3.2rem;
    }

    @media screen and (max-width: 460px) {
      height: 2rem;
      width: 2.4rem;
    }

    &.actived {
      .line {
        &:first-child {
          top: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          width: 100%;
          transition:
            top 0.3s linear,
            transform 0.3s linear 0.5s;
        }

        &:nth-child(2) {
          opacity: 0;
          transition: opacity 0.1s ease 0.3s;
        }

        &:last-child {
          bottom: 50%;
          transform: translate(-50%, 50%) rotate(-45deg);
          width: 100%;
          transition:
            bottom 0.3s linear,
            transform 0.3s linear 0.5s;
        }
      }
    }

    .line {
      width: 100%;
      display: block;
      height: 0.2rem;
      background-color: $color-pri;
      transform: translateX(-50%);
      border-radius: 100rem;
      overflow: hidden;
      position: absolute;

      &:first-child {
        top: 0;
        left: 50%;
        width: 100%;

        transition:
          top 0.3s linear 0.5s,
          transform 0.3s linear 0s;
      }

      &:nth-child(2) {
        top: 50%;
        left: 0;
        width: 100%;
        transform: translate(0%, -50%);
        transition: opacity 0.1s ease 0.3s;
      }

      &:last-child {
        width: 100%;
        bottom: 0%;
        left: 50%;
        transition:
          bottom 0.3s linear 0.5s,
          transform 0.3s linear 0s;
      }
    }
  }

  &-box {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.6rem;

    @media screen and (max-width: 1200px) {
      column-gap: 1.2rem;
    }

    @media screen and (max-width: 800px) {
      column-gap: 0.8rem;
    }

    @media screen and (max-width: 600px) {
      column-gap: 0.6rem;
    }

    .btn {
      flex-shrink: 0;

      width: fit-content;

      @media screen and (max-width: 1200px) {
        min-width: unset;
      }

      &-ic {
        display: none;

        @media screen and (max-width: 500px) {
          display: flex;
        }
      }

      &-text {
        @media screen and (max-width: 500px) {
          display: none;
        }
      }

      @media screen and (max-width: 500px) {
        padding: 1.2rem 1.4rem;
      }
    }
  }

  &-login {
    height: 100%;
    // display: flex;
    // align-items: center;
  }
}
