.ct {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &.other {
        .container {
            max-width: 124.5rem;

            .ct_wrap {
                width: auto;
                margin: 0 -1.6rem;

                @media screen and (max-width: 1300px) {
                    margin: 0 -1.2rem;
                    width: auto;
                }

                @media screen and (max-width: 1024px) {
                    margin: 0 -1rem;
                    width: auto;
                }
            }
        }
    }

    .container {
        max-width: 153rem;
    }
    &_wrap {
        width: 100%;
        margin: 0 auto;
        row-gap: var(--rowContent3);
        @media screen and (max-width: 1300px) {
            margin: 0 -1.2rem;
            width: auto;
        }

        @media screen and (max-width: 1024px) {
            margin: 0 -1rem;
            width: auto;
        }


    }

    &_lf {
        width: calc(100% * 3 / 12);
        &--wrap {
            height: 100%;
        }

        .box-ml {
            position: sticky;
            top: 15rem;

            @media screen and (max-width: 1200px) {
                top: 12rem;
            }

            #ez-toc-container {
                width: 100%;
                background: $color-white;
                padding: 2.4rem;
                border-radius: var(--radius);
                display: flex;
                flex-direction: column;
                row-gap: 1rem;
                border: 0.1rem solid #cdb29f;
                margin-bottom: 0;

                .ez-toc-title-container {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    column-gap: var(--columnContent1);
                }
                a:visited {
                    color: $color-pri;
                }

                @media screen and (max-width: 1400px) {
                    padding: 2rem;
                }

                @media screen and (max-width: 1200px) {
                    padding: 1.2rem;
                }
            }
        }

        @media screen and (max-width: 1400px) {
            width: calc(100% * 2.5 / 12);
        }

        @media screen and (max-width: 1024px) {
            width: calc(100% * 3.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }
    }

    &_ct {
        width: 100%;
        flex: 1;

        &--mxh {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--slide {
            position: relative;

            .btn-navi {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                &.prev {
                    left: 0;
                }

                &.next {
                    right: 0;
                }
            }

            .swiper-slide {
                height: auto;

                @media screen and (max-width: 800px) {
                    width: calc(100% / 3);
                }

                @media screen and (max-width: 460px) {
                    width: calc(100% / 2);
                }
            }

            &-item {
                height: 100%;
            }

            &-img {
                padding-top: calc(100% * 344 / 249);
                width: 100%;
                display: block;
                position: relative;
                overflow: hidden;

                @include hover {
                    &::before {
                        opacity: 1;
                    }

                    &::after {
                        opacity: 1;
                    }
                }

                &::before {
                    content: "";
                    position: absolute;
                    inset: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba($color: #000000, $alpha: 0.5);
                    z-index: 1;
                    opacity: 0;
                    transition: 0.3s linear;
                }

                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    background-image: url(../assets/images/zoom2.svg);
                    width: 4rem;
                    height: 4rem;
                    transform: translate(-50%, -50%);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;
                    pointer-events: none;
                    z-index: 10;
                    opacity: 0;
                    transition: 0.3s linear;
                }

                img {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    object-fit: cover;
                }
            }
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            .sp_ct--ct-btn .single_add_to_cart_button{
                background: #384259;

                @include hover{
                    background: $color-second;
                }

                i{
                    color: $color-white;
                    @include fz-20;
                    font-style: normal;
                }
            }

            .quantity-btn i{
                font-style: normal !important;
            }
        }
        &--img {
            img {
                display: block;
                width: 100%;
                height: 100%;
                margin: 0 auto;
                object-fit: contain;
            }
        }

        &--box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--title {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &--infos {
            display: flex;
            align-items: center;
            column-gap: 1.6rem;

            @media screen and (max-width: 600px) {
                flex-wrap: wrap;
                column-gap: var(--columnContent1);
                row-gap: var(--rowContent0);
            }
        }
        &--info {
            &:not(:last-child) {
                padding-right: 1.6rem;
                border-right: 0.1rem solid $color-gray10;

                @media screen and (max-width: 600px) {
                    padding-right: 0;
                    border: none;
                }
            }
            &-text {
                padding-left: 2.4rem;

                position: relative;
                display: -webkit-box;

                @media screen and (max-width: 600px) {
                    @include fz-14;
                    padding-left: 2rem;
                }

                @media screen and (max-width: 460px) {
                    @include fz-12;
                }

                &.author {
                    &::before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0%;
                        transform: translateY(-50%);
                        background-image: url(../assets/images/author.svg);
                        width: 2rem;
                        height: 2rem;
                        background-size: 100% 100%;

                        @media screen and (max-width: 600px) {
                            top: 49%;
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                }

                &.date {
                    &::before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0%;
                        transform: translateY(-50%);
                        background-image: url(../assets/images/time1.svg);
                        width: 2rem;
                        height: 2rem;
                        background-size: 100% 100%;

                        @media screen and (max-width: 600px) {
                            top: 49%;
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    color: #4d4d4d;
                }

                &.read {
                    &::before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0%;
                        transform: translateY(-50%);
                        background-image: url(../assets/images/time1.svg);
                        width: 2rem;
                        height: 2rem;
                        background-size: 100% 100%;

                        @media screen and (max-width: 600px) {
                            top: 49%;
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    color: #4d4d4d;
                }
            }
        }

        &--content {
            padding-bottom: var(--size-pd);
            border-bottom: 0.1rem solid $color-gray10;
        }

        &--imgs {
            display: flex;
            column-gap: var(--columnContent1);

            &--img {
                width: calc(100% - var(--columnContent1));
            }
        }
    }

    &_rt {
        width: calc(100% * 3.5 / 12);

        @media screen and (max-width: 1400px) {
            width: calc(100% * 3 / 12);
        }

        @media screen and (max-width: 1024px) {
            width: 100%;
        }

        &--wrap {
            height: 100%;

            .sp_btns {
                display: none;
            }
        }

        &--box {
            padding: 2.4rem;
            background: $color-white;
            border-radius: var(--radius);
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
            position: sticky;
            top: 15rem;

            @media screen and (max-width: 1400px) {
                padding: 2rem;
            }

            @media screen and (max-width: 1200px) {
                padding: 1.2rem;
                top: 12rem;
            }
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: 1.2rem;

            @media screen and (max-width: 1024px) {
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--columnContent1);
            }

            @media screen and (max-width: 460px) {
                flex-direction: column;
                row-gap: var(--rowContent1);
            }
        }

        &--item {
            .sp_wrap {
                padding: 0;
                flex-direction: row;
                border-radius: 0;
                background: transparent;
                column-gap: var(--columnContent2);

                @include hover {
                    box-shadow: none;
                }

                @media screen and (max-width: 1400px) {
                    column-gap: var(--columnContent1);
                }
            }

            .sp_top {
                width: 35%;

                flex-shrink: 0;
                .sp_img--wrap {
                    max-width: 12.4rem;
                    height: 100%;

                    @media screen and (max-width: 1024px) {
                        max-width: none;
                    }
                }

                .sp_img {
                    padding-top: calc(100% * 78 / 124);
                    border: 0.1rem solid #e8ebec;
                    border-radius: var(--radius);
                    height: 100%;
                }

                @media screen and (max-width: 1024px) {
                    width: 25%;
                }
            }
            .sp_bottom {
                display: flex;
                flex-direction: column;
                justify-content: center;
                // padding: 0.5rem 0;
                flex: 1;
                width: calc(100% - var(--columnContent2));
                row-gap: var(--rowContent0);

                .sp_ct--ct-title-bottom-it {
                    display: none;
                }

                .sp_ct--ct-price {
                    .price-normal {
                        @include fz-16;
                        @media screen and (max-width: 1200px) {
                            @include fz-14;
                        }
                    }

                    .price-line {
                        @include fz-16;
                        @media screen and (max-width: 1200px) {
                            @include fz-14;
                        }
                    }

                    .info-price-current {
                        @include fz-14;
                        gap: 0.5rem;
                        display: -webkit-box;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        white-space: wrap;
                        .woocommerce-Price-amount.amount {
                            @include fz-16;
                            color: $color-pri;
                            @media screen and (max-width: 1200px) {
                                @include fz-14;
                            }
                        }
                    }
                }

                .sp_name {
                    @media screen and (max-width: 1200px) {
                        @include fz-14;
                    }
                }
            }

            @media screen and (max-width: 1024px) {
                width: calc(50% - (var(--columnContent1) / 2));
            }

            @media screen and (max-width: 460px) {
                width: 100%;
            }
        }
    }
}

.box-ml {
    &--wrap {
        background: $color-white;
        padding: 2.4rem;
        border-radius: var(--radius);
        display: flex;
        flex-direction: column;
        row-gap: 1rem;
        border: 0.1rem solid #cdb29f;

        @media screen and (max-width: 1400px) {
            padding: 2rem;
        }

        @media screen and (max-width: 1200px) {
            padding: 1.2rem;
        }

        .note-lg {
            @include fz-16;
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: 0.5rem;

        .note-text {
            @media screen and (max-width: 1200px) {
                @include fz-14;
            }
        }

        ul {
            display: flex;
            flex-direction: column;
            row-gap: 0.1rem;
            padding-left: 3.2rem;
            list-style: decimal;

            @media screen and (max-width: 1200px) {
                padding-left: 2.8rem;
            }

            li {
                @media screen and (max-width: 1200px) {
                    @include fz-14;
                }
                a {
                    color: #393d48;
                    @include fz-16;

                    @media screen and (max-width: 1200px) {
                        @include fz-14;
                    }

                    @include hover {
                        color: $color-pri;
                    }

                    &.actived {
                        color: $color-pri;
                    }
                }
            }
        }
    }
}

.news_ct {
    &--note {
        padding: 0 1.2rem;
        border-left: 0.3rem solid $color-brown2;
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;

        @media screen and (max-width: 1200px) {
            row-gap: 0.6rem;
        }

        @media screen and (max-width: 800px) {
            row-gap: 0.4rem;
        }

        @media screen and (max-width: 600px) {
            row-gap: 0.2rem;
        }

        .note-sm {
            color: #a3a3a3;
            font-weight: 400;
        }

        &.cus {
            padding: 1.6rem;
            background: $color-brown2;
            border-left: 0.3rem solid #244233;

            @media screen and (max-width: 600px) {
                padding: 1.2rem;
            }

            .note-sm {
                color: $color-white;
            }
        }
    }
}
