.menu {
  &-list {
    display: flex;

    margin-left: -2.4rem;
    margin-right: -2.4rem;
    height: 100%;
  }

  &-ic {
    margin-top: 0.5rem;
    width: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      font-size: 1.2rem;
      font-weight: 600;
      line-height: 1.5;
    }
  }

  &-item {
    // padding: 0 1.5rem;
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;

    &.current-menu-item {
      & > .menu-link {
        color: $color-pri;
        font-weight: 700;

        &::before {
          width: calc(100% - 3.2rem);
        }
      }
    }

    &.dropdown {
      // @keyframes hienLen2 {
      //     0% {
      //         transform: translateY(5%);
      //         opacity: 0;
      //     }

      //     50% {
      //         transform: translateY(2.5%);
      //         opacity: 0.5;
      //     }

      //     100% {
      //         transform: translateY(0%);
      //         opacity: 1;
      //     }
      // }

      @include hover {
        & > .menu-list {
          opacity: 1;
          visibility: visible;
          transform: translateY(0%);
          pointer-events: visible;
        }
      }
    }

    & > .menu-list {
      position: absolute;
      margin-left: 0;
      margin-right: 0;
      top: calc(100% + 0.1rem);
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      left: 1.6rem;
      display: flex;
      flex-direction: column;
      min-width: 20rem;
      height: auto;
      background: $color-white;
      transform: translateY(30%);
      box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.1);
      transition: all 0.3s linear;
      z-index: 2;
      transition: all 0.5s ease;

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2rem;
        background-color: transparent;
        top: -10%;
        left: 0;
      }

      .menu-item {
        padding: 0;

        &.current-menu-item {
          .menu-link {
            color: $color-pri;
            font-weight: 600;
          }
        }

        &.dropdown > .menu-list {
          left: unset !important;
          right: -100%;
          top: 0 !important;
        }
      }

      .menu-link {
        width: 100%;
        border-radius: 0;
        padding: 1rem 1.2rem;
        color: $color-text2;
        justify-content: space-between;
        &::before {
          display: none;
        }
      }
    }

    @include hover {
      & > .menu-link {
        color: $color-pri;

        &::before {
          width: calc(100% - 3.2rem);
        }
      }
    }
  }

  &-link {
    @include fz-18;
    padding: 0rem 2.4rem;
    color: $color-third;
    font-weight: 600;
    column-gap: 0.6rem;
    display: flex;
    align-items: center;
    white-space: nowrap;
    position: relative;
    width: fit-content;
    height: 100%;
    // text-transform: uppercase;
    &::before {
      content: "";
      position: absolute;
      bottom: 0%;
      left: 1.6rem;
      right: 1.6rem;
      width: 0;
      height: 0.3rem;
      border-radius: 10rem;
      background: $color-pri;
      transition: all 0.3s linear;
    }
  }
}

.menu-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba($color: #000000, $alpha: 0.6);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s linear;
  z-index: 1999;

  &.actived {
    opacity: 1;
    visibility: visible;
  }
}

