.menu-mb {
  position: fixed;
  // background: linear-gradient(0deg, #142f5e 0%, #14213f 39.53%, #141829 76.36%, #141421 100%);
  background: white;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.2);

  width: 35%;
  height: calc(100% - var(--size-hd));
  top: var(--size-hd);
  left: 0%;
  z-index: 2000;
  padding: 0rem 0rem 1rem 0rem;
  transform: translateX(-100%);
  visibility: hidden;
  pointer-events: none;
  transition: 0.8s ease;

  .btn {
    width: fit-content;
    margin: 0 auto 2rem;
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    height: 80vh;
    overflow-y: auto;
    overflow-x: hidden;
    // width: 100%;
    margin: 0;
  }

  .list-address {
    row-gap: 1rem;
    padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
  }

  .ic-address {
    border: none !important;
    border-radius: unset !important;
    width: 2rem;
    // height: 1.8rem;
    height: 100%;
    flex-shrink: 0;
    // margin-top: -0.3rem;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      filter: brightness(0) saturate(100%) invert(8%) sepia(8%) saturate(4331%) hue-rotate(184deg) brightness(93%)
        contrast(98%);
    }
  }

  .item-address {
    // align-items: center !important;
    column-gap: 1rem;
    flex-direction: row;
    display: flex;
  }

  .link-address {
    color: $color-third;
    font-weight: 600;
    font-size: 1.4rem;
  }

  .menu-item {
    width: 100%;
    height: unset;
    flex-direction: column;
    align-items: unset;
    // padding-right: 2.4rem;
    padding: 0;
    pointer-events: visible;
    &::before {
      display: none;
    }

    &:not(:last-child) {
      border-bottom: 0.1rem solid rgba($color: #000000, $alpha: 0.3);
    }
    &.current-menu-item {
      & > .menu-link {
        color: $color-pri;
        font-weight: 600;

        &::before {
          display: none;
        }
      }
    }

    &-box {
      display: flex;
      column-gap: 1rem;
      align-items: center;
      justify-content: space-between;
      padding-right: 1.2rem;
    }

    &.dropdown {
      & > .menu-list {
        position: relative;
        transform: none;
        top: 0;
        left: 0 !important;
        opacity: 1;
        visibility: visible;
        min-width: unset;
        background-color: transparent;
        box-shadow: none;
        margin-left: 1.2rem;
        display: none;
        height: auto;
        // padding-bottom: 1rem;
        transition: none;
        height: 100%;
        &::before {
          content: "";
          position: absolute;
          top: 1rem !important;
          left: 0;
          height: auto;
          bottom: 1rem !important;
          width: 0.1rem;

          background: $color-pri;
        }

        &.showed {
          display: block;
        }

        & > .menu-item {
          border-bottom: none;

          &.current-menu-item {
            .menu-link {
              color: $color-pri;
            }
          }

          .menu-list {
            left: 0 !important;
            right: unset !important;
          }
          .menu-list > .menu-item {
            .menu-list > .menu-item > .menu-link {
              font-size: 1.3rem;
            }
          }

          & > .menu-link {
            width: 100%;
            color: $color-third;
            padding: 0.6rem 1.2rem;
            font-size: 1.4rem;
            font-weight: 600;
            // text-transform: uppercase;
            justify-content: space-between;
          }
        }
      }
    }

    .menu-link {
      width: 100%;
      padding: 0.8rem 1.2rem;
      justify-content: space-between;
      height: auto;
      // @include fz-16;
      font-size: 1.6rem !important;
      font-weight: 700;
      white-space: unset;
    }
  }

  .menu-ic {
    flex-shrink: 0;
    width: 2rem;
    height: 100%;
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      @include fz-16;
      color: $color-black;
      transition: all 0.3s linear;
    }

    // margin-top: -0.3rem;
    cursor: pointer;
    transition: all 0.3s linear;

    &.actived {
      transform: rotate(-180deg);
    }
  }

  &.actived {
    visibility: visible;
    pointer-events: visible;
    transform: translateX(0);
  }

  @media screen and (max-width: 1024px) {
    width: 50%;
  }

  @media screen and (max-width: 800px) {
    width: 60%;
  }

  @media screen and (max-width: 600px) {
    width: 70%;
  }

  @media screen and (max-width: 500px) {
    width: 80%;
  }

  @media screen and (max-width: 460px) {
    width: 100%;
  }

  @media screen and (max-width: 360px) {
    width: 100%;
  }

  &-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
  }
}

