.menu-mega {
  // padding-bottom: 0.5rem;
  // width: 120rem;

  &-wrap {
    // width: 100%;
    margin-left: 0 !important;
    margin-right: 0 !important;

    overflow: hidden;
    column-gap: 0.6rem;
    position: relative;
  }

  &.showed {
  }

  &-modal {
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    background: rgba($color: #000000, $alpha: 0.5);
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: 0.1s ease;
    z-index: 1000;
  }

  &-lf {
    padding-left: 0 !important;
    padding-right: 0 !important;
    width: calc(100% * 3 / 12 - 1.8rem);
    // margin-right: 0.3rem;
    position: relative;
    border-radius: var(--radius);

    &-wrap {
      // padding-top: 6rem;
      background: #f7f7f7;
      height: 100%;
      border-radius: var(--radius);
      // overflow: hidden;
    }

    &-list {
      min-height: 60vh;
      // overflow-y: auto;
      border-radius: var(--radius);

    }

    &-item {

      &:first-child{
        border-top-left-radius: var(--radius);
        border-top-right-radius: var(--radius);

        &::before{
          border-top-left-radius: var(--radius);
        border-top-right-radius: var(--radius);
        }

      }
      
      &:not(:last-child) {
        border-bottom: 0.1rem solid transparent;
      }

      position: relative;

      &::before {
        content: "";
        background: transparent;
        height: 100%;
        width: 6rem;
        position: absolute;
        top: 0;
        right: -2rem;
        z-index: 1200;

      }

      &.actived {
        background: #ededed;

        .note-md {
          // color: $color-white;
        }
        .menu-mega-lf-ic {
          // img {
          //   filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(333deg)
          //     brightness(105%) contrast(103%);
          // }
        }
      }
    }

    &-link {
      padding: 0.8rem 3rem 0.8rem 1.1rem;
      border: none;
      display: flex;
      column-gap: 0.6rem;
      background: transparent;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 1rem;
        background-image: url(../assets/images/arrh.png);
        width: 2rem;
        height: 2rem;
        background-size: 100% 100%;
        background-repeat: no-repeat;
      }

      .note-md {
        color: $color-third;
        font-weight: 600;
      }

      // @include hover {
      //   background: $color-brown2;
      //   .note-md {
      //     color: $color-white;
      //   }
      //   .menu-mega-lf-ic {
      //     img {
      //       filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(333deg)
      //         brightness(105%) contrast(103%);
      //     }
      //   }
      // }
    }

    &-ic {
      width: 2.4rem;
      height: 2.4rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 0.35rem;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &-title {
    position: relative;
    font-weight: 700;
    @include fz-16;
    width: fit-content;
    color: $color-third;
    display: -webkit-box;

    @include hover {
      color: $color-third;

      &:before {
        background: $color-third;
      }
    }

    &::before {
      content: "";
      width: 100%;
      position: absolute;
      left: 0;
      bottom: -0.25rem;
      background: #f0f0f0;
      height: 0.2rem;
      transition: 0.3s linear;
    }
  }

  &-rt {
    padding-left: 0 !important;
    padding-right: 0 !important;
    position: relative;
    width: 100%;
    z-index: 1000;
    flex: 1;
    display: none;
    border-radius: var(--radius);
    // padding-left: 0.3rem;

    &.showed {
      display: block;
    }

    &-wrap {
      // width: 100%;
      height: 60vh;
      background: #f7f7f7;
      border-radius: var(--radius);

      overflow-y: auto;
    }

    &-list {
      // width: 100%;
      position: relative;
      height: 100%;
    }

    &-item {
      display: none;
      position: relative;

      padding-left: 1.2rem;
      height: 100%;

      &-wrap {
        height: 100%;
      }

      &.showed {
        display: block;
        // animation: anHienMg 0.6s linear forwards;
      }
      &-list {
        display: flex;
        // justify-content: space-between;
        row-gap: 0.4rem;
        flex-wrap: wrap;
        column-gap: 1.2rem;
      }

      &-item {
        width: calc(100% / 4 - 1.2rem);
        // background: red;
      }

      &-title {
        height: 4rem;
        display: flex;
        align-items: center;

        &.actived {
          .menu-mega-title {
            &::before {
              background: $color-third;
            }
          }
        }
      }
    }
  }

  &-list {
    // border-left: 0.2rem solid #f0f0f0;
    padding: 0 0rem;
    max-height: 30vh;
    overflow-y: auto;
  }

  &-link {
    padding: 1rem 0rem;
    display: block;
    @include fz-14;
    font-weight: 600;
    color: $color-third;
    position: relative;
    width: fit-content;
    transition: 0.01s linear;
    @include hover {
      font-weight: 600;
      color: $color-pri;

      &::before {
        width: 100%;
      }
    }

    &::before {
      content: "";
      bottom: 1rem;
      height: 0.1rem;
      width: 100%;
      background: $color-pri;
      position: absolute;
      width: 0;
      transition: 0.3s linear;
    }
  }
}
