.btn {
    display: flex;
    // align-items: center;
    justify-content: center;
    cursor: pointer;
    column-gap: 0.8rem;
    height: 4rem;
    width: fit-content;
    padding: 0 2.4rem;
    min-width: 18rem;
    // border-radius: 0.8rem;
    background: $color-pri;
    position: relative;
    overflow: hidden;
    // flex-direction: row-reverse;
    border-radius: var(--radius);
    transition: 0.5s linear;
    @media screen and (max-width: 1200px) {
        column-gap: 0.6rem;
        min-width: 12rem;
        height: 3.6rem;
    }

    @media screen and (max-width: 800px) {
        height: 3.2rem;
        padding: 0 1.2rem;
        column-gap: 0.6rem;
    }

    @media screen and (max-width: 600px) {
        height: 3.2rem;
        padding: 0 1.2rem;
        min-width: 12rem;
    }

    &.cl-2 {
        background: $color-white;
        box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.3);
        .btn-text {
            color: $color-third;
        }

        .btn-ic{
            i{
            color: $color-third;

            }
        }

        &::before {
            background: $color-pri;
        }

        @include hover {
            background: $color-pri;
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.cl-3 {
        background: #F0D9C9;

        .btn-text {
            color: $color-black;
        }

        .btn-ic {
            i {
                color: $color-black;
            }
        }

        &::before {
            background: $color-second;
        }

        @include hover {
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.cl-red{
        background: $color-red;
     
        .btn-text {
            color: $color-white;
        }

        .btn-ic{
            i{
            color: $color-white;

            }
        }

        &::before {
            background: $color-pri;
        }

        @include hover {
            background: $color-pri;
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.blue{
        background: $color-second;
     
        .btn-text {
            color: $color-white;
        }

        .btn-ic{
            i{
            color: $color-white;

            }
        }

        &::before {
            background: $color-pri;
        }

        @include hover {
            background: $color-pri;
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.trans {
        background: transparent;
        border: 0.1rem solid $color-pri;
        
        .btn-text {
            color: $color-pri;
        }

        .btn-ic i {
            color: $color-pri;
        }

        &::before {
            background: $color-pri !important;
        }

        @include hover {
            border-color: $color-pri;

            .btn-text {
                color: $color-white;
            }
    
            .btn-ic i {
                color: $color-white;
            }
        }
    }

    &-text {
        font-weight: 600;
        color: $color-white;
        position: relative;
        z-index: 2;
        height: 100%;
        transition: all 0.5s ease;
        white-space: nowrap;
        display: flex;
        align-items: center;
        justify-content: center;
        @include fz-16;
        @media screen and (max-width: 460px) {
            @include fz-12;
        }
    }

    &-ic {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        flex-shrink: 0;
        height: 100%;
        i {
            font-size: 1.2rem;
            color: $color-white;
            transition: all 0.3s linear;
            top: 0.1rem;
            line-height: 100%;
            position: relative !important;
            @media screen and (max-width: 800px) {
                position: relative;
                top: 0.1rem;
                font-size: 1rem;
            }

            @media screen and (max-width: 600px) {
                position: relative;
                top: 0.1rem;
                font-size: 1rem;
            }

            @media screen and (max-width: 460px) {
                position: relative !important;
                top: 0.1rem;
                font-size: 0.8rem;
            }
        }

        img {
            width: 1.8rem;
            height: 1.8rem;
            object-fit: contain;
            transform: translateY(-0.1rem);
        }
    }

    &::before {
        content: "";
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background: $color-second;
        border-radius: var(--radius);
        z-index: 1;
        overflow: hidden;
        transition: 0.3s linear;
        opacity: 0;
    }

    @include hover {
        // transition: 0.3s linear;
        background: $color-second;
        &::before {
            opacity: 1;
        }

        .btn-text {
            color: $color-white;
        }

        .btn-ic {
            i {
                color: $color-white;
            }
        }
    }
}

.btn-add-cart {
    width: fit-content;
    // height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0.1rem solid $color-pri !important;
    border-radius: var(--radius);
    background: transparent !important;
    transition: 0.3s linear ;
    i {
        color: $color-pri !important;
        @include fz-24;
        transition: 0.3s linear;
    }

    @include hover {
        background: $color-second !important;
        border-color: $color-second !important;
        i {
            color: $color-white !important;
        }
    }
}

.box-navi {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: end;
    column-gap: 1rem;
    .btn-navi {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 3rem;
        height: 3rem;
        background: $color-gray;
        // position: absolute;
        border-radius: 0.4rem;

        z-index: 5;
        cursor: pointer;
        transition: all 0.3s linear;

       
        @media screen and (max-width: 600px) {
            width: 2.6rem;
            height: 2.6rem;
        }
        &.prev {
            // top: 45%;
            // left: 0rem;
        }

        &.next {
            // top: 45%;
            // right: 0rem;
        }

        @include hover {
            background: $color-pri;

            i {
                color: $color-white;
            }
        }

        i {
            font-size: 1.2rem;
            color: $color-text2;
            transition: all 0.3s linear;

            @media screen and (max-width: 1200px) {
                font-size: 1.2rem;
            }
        }
    }
}


.box-date{
    display: flex;
    align-items: center;
    column-gap: 1.6rem;
    width: fit-content;

    @media screen and (max-width:460px) {
        column-gap: 1.2rem;
    }


    .note-sm{
        position: relative;
        color: $color-gray3;
        // white-space: nowrap;
        transition: 0.3s linear;

        @media screen and (max-width:460px) {
            @include fz-12;
        }

        &.title{
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;  
            overflow: hidden;
        }

        &:not(:last-child){
            &::before{
                content: "|";
                position: absolute;
                top: 45%;
                right: -1rem;
                transform: translateY(-50%);
                color: #e0e0e0;

                @media screen and (max-width:460px) {
                    right: -0.8rem;
                }
        
            }
        }
    }
}



.box-napa{
    width: 100%;
    margin-top: 3rem;

    @media screen and (max-width: 1200px) {
        margin-top: 2.4rem;
    }

    @media screen and (max-width: 800px) {
        margin-top: 2rem;
    }

    @media screen and (max-width: 600px) {
        margin-top: 1.6rem;
    }


    &-wrap{
        display: flex;
        align-items: center;
        column-gap: var(--columnContent);
        justify-content: center;
        width: 100%;
        .btn-navi {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 3rem;
            height: 3rem;
            background: $color-gray;
            // position: absolute;
            border-radius: 0.4rem;
    
            z-index: 5;
            cursor: pointer;
            transition: all 0.3s linear;
    
         
    
            @media screen and (max-width: 600px) {
                width: 2.6rem;
                height: 2.6rem;
            }
            &.prev {
                // top: 45%;
                // left: 0rem;
            }
    
            &.next {
                // top: 45%;
                // right: 0rem;
            }
    
            @include hover {
                background: $color-pri;
    
                i {
                    color: $color-white;
                }
            }
    
            i {
                font-size: 1.2rem;
                color: $color-text2;
                transition: all 0.3s linear;
    
                @media screen and (max-width: 1200px) {
                    font-size: 1.2rem;
                }
            }
        }

        .swiper-pagination {
            width: fit-content;
            margin-top: 0;
        }
    }
}