:root {
    --size-hd: 14rem;
    --size-pd: 3rem;
    --size-pdl: 6rem;

    --font-pri: "Mulish";
    // --height-hd: 40rem;
    // --height-hd2: 10rem;

    --rowContent: 4rem;
    --rowContent2: 2rem;
    --rowContent3: 3rem;
    --radius: 0.8rem;
    --rowContent1: 1.6rem;
    --rowContent0: 0.8rem;
    --rowContent6: 6rem;
    --rowContent4: 4.8rem;



    --columnContent: 3.2rem;
    --columnContent1: 1.6rem;
    --columnContent2: 2rem;

    @media screen and (max-width: 1200px) {
        --radius: 0.6rem;
        --size-hd: 10.2rem;
        --size-hd2: 12rem;
        --size-pd: 2rem;
        --size-pdl: 5rem;
        --rowContent: 3rem;
        --rowContent0: 0.6rem;
        --rowContent1: 1.4rem;
        --rowContent2: 1.6rem;
        --rowContent3: 2.4rem;
        --rowContent4: 3.2rem;
        --rowContent6: 5rem;
        --columnContent: 1.6rem;
        --columnContent1: 1.4rem;
    }

    @media screen and (max-width: 800px) {
        --size-pd: 1.6rem;
        --size-pdl: 4rem;
        --rowContent: 2.4rem;
        --rowContent0: 0.4rem;
        --rowContent1: 1.2rem;
        --rowContent2: 1.4rem;
        --rowContent3: 2rem;
        --rowContent6: 4rem;
        --rowContent4: 2.4rem;
        --columnContent: 1.4rem;
        --columnContent1: 1.2rem;
    }

    @media screen and (max-width: 600px) {
        --radius: 0.4rem;
        --size-hd: 9.2rem;
        --size-hd2: 10rem;
        --size-pd: 1.2rem;
        --size-pdl: 3rem;
        --rowContent: 2rem;
        --rowContent0: 0.3rem;
        --rowContent1: 1rem;
        --rowContent2: 1.4rem;
        --rowContent3: 1.8rem;
        --rowContent6: 3rem;
        --columnContent: 1.2rem;
        --columnContent1: 1rem;
        --rowContent4: 2rem;

    }

    @media screen and (max-width: 460px) {
        --columnContent: 0.8rem;
        --size-hd: 7.4rem;
        --rowContent: 1.6rem;

    }
}

