.nb {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--columnContent);
    }

    &_box {
        border: 0.1rem solid #e0e0e0;

        .box-date .note-sm.date {
            &::before {
                display: none;
            }
        }
    }

    &_img {
        padding-top: calc(100% * 320 / 1216);
        width: 100%;
        position: relative;
        overflow: hidden;

        @media screen and (max-width: 600px) {
            padding-top: calc(100% * 200 / 600);
        }

        @media screen and (max-width: 460px) {
            padding-top: calc(100% * 200 / 460);
        }

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &_list {
        display: flex;
        flex-wrap: wrap;

        @media screen and (max-width: 600px) {
            flex-direction: column;
        }
    }

    &_item {
        width: calc(100% / 3);

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        .post_img {
            display: none;
        }

        .post_wrap {
            box-shadow: none;

            @include hover {
                background: $color-second;

                .box-date {
                    .date {
                        color: $color-white;
                    }
                }

                .post_bottom {
                    .post_name {
                        color: $color-white !important;
                    }
                }

                .note-link {
                    color: $color-white;

                    &::before {
                        filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7446%) hue-rotate(261deg)
                            brightness(115%) contrast(111%);
                    }
                }
            }
        }

        .post_bottom {
            border: none;
            .box-date {
                .title {
                    display: none;
                }
            }
        }
        &:not(:first-child) {
            .post_wrap {
                border-left: 0.1rem solid #e0e0e0;

                @media screen and (max-width: 600px) {
                    border-left: none !important;
                }
            }
        }

        @media screen and (max-width: 600px) {
            &:not(:last-child) {
                border-bottom: 0.1rem solid #e0e0e0;
            }
        }

        &:nth-child(3n + 1) {
            .post_wrap {
                border-left: none;
            }
        }
    }
}

.build {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &.actived{


        .pro_choose--filter{
            position: fixed;
            width: 100%;
            top: 10.5rem;
            left: 0;
            animation: anHienTop 0.5s linear;
            z-index: 900;
            padding: 1rem 0;

            @media screen and (max-width:1200px) {
                top: 10.2rem;
            }

            @media screen and (max-width:600px) {
                top: 9.2rem;
            }

            @media screen and (max-width:460px) {
                top: 7.4rem;
            }
        }
       
        .pro_choose--filter-wrap{
            row-gap: var(--rowContent0);

            .title-main{
                display: none;
            }
        }
    }

    &_filter {
        padding: 1.2rem 0;
        background: #e6e4e4;
        animation: anHienTop2 0.5s linear;
        &-list {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent2);
            flex-wrap: wrap;
            // row-gap: var(--rowContent2);
            width: fit-content;
            margin: 0 auto;

            margin: 0 auto;
            align-items: center;
            justify-content: center;
            width: 100%;
            .swiper {
                overflow-x: hidden;
                overflow-y: auto;
                width: fit-content;
                // overflow: visible;
            }

            // &.d-wrap{
            //     margin-left: -0.8rem;
            //     margin-right: -0.8rem;
            // }

            .swiper-scrollbar{
                @media screen and (max-width:600px) {
                    margin-top: 0.5rem;
                }
            }

        .swiper-slide {
                height: auto;
                width: fit-content;

                &.d-item {
                    padding-left: 0.8rem;
                    padding-right: 0.8rem;

                    @media screen and (max-width: 800px) {
                        padding-left: 0.4rem;
                        padding-right: 0.4rem;
                    }
                }
            }

            @media screen and (max-width: 1200px) {
                // justify-content: space-between;
                column-gap: var(--columnContent1);
            }

            @media screen and (max-width: 600px) {
                justify-content: space-between;
                column-gap: var(--columnContent0);
            }


            // @media screen and (max-width: 1200px) {
            //     column-gap: var(--columnContent1);
            //     row-gap: var(--rowContent1);
            //     justify-content: center;
            // }
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: 0.5;

            .title-main{
                @include fz-28;
            }
        }

        &-item {
            width: fit-content;
            border-radius: var(--radius);
            padding: 0.6rem 1.6rem;
            background: $color-white;
            border: 0.1rem solid transparent;
            transition: 0.3s linear;
            overflow: hidden;
            cursor: pointer;
            @include hover {
                border-color: $color-text;
            }

            @media screen and (max-width: 1200px) {
                padding: 0.4rem 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.2rem 0.8rem;
            }

            .note-lg {
                color: $color-third !important;
                @media screen and (max-width: 1200px) {
                    @include fz-16;
                }

                @media screen and (max-width: 600px) {
                    @include fz-14;
                }
            }

            &.actived {
                background: $color-brown2;
                .note-lg {
                    color: $color-white !important;
                }
            }
        }
    }

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent6);
    }

    &_content {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            .page-numbers {
                margin: 0 auto;
            }
        }

        &--list {
            row-gap: var(--rowContent);
        }

        &--item {
            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }
}
