.sp_ct {
    padding-top: var(--size-pd);
    &--wrap {
        row-gap: var(--rowContent);
    }

    &--lf {
        width: calc(100% * 5 / 12);

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &-top {
            padding: 1.4rem 3rem;
            background: $color-white;
            border-radius: var(--radius);
            position: relative;

            @media screen and (max-width: 1200px) {
                padding: 1.2rem 2rem;
            }

            @media screen and (max-width: 460px) {
                padding: 1.2rem;
            }

            &-wrap {
                // max-width: 48rem;
                position: relative;
                width: 100%;

                .swiper-slide {
                    height: auto;
                }

                .btn-navi {
                    z-index: 5;
                    &.prev {
                        position: absolute;
                        top: 50%;
                        left: 0rem;
                        transform: translateY(-50%);
                    }

                    &.next {
                        position: absolute;
                        top: 50%;
                        right: 0rem;
                        transform: translateY(-50%);
                    }
                }
            }

            &-item {
                height: 100%;
                width: 100%;
                position: relative;

                @media screen and (max-width: 800px) {
                    max-width: 60rem;
                    margin: 0 auto;
                }

                &::after {
                    content: "";
                    position: absolute;
                    top: 0rem;
                    right: 0rem;
                    background-image: url(../assets/images/zoom.svg);
                    width: 4rem;
                    height: 4rem;
                    background-size: 100% 100%;
                    background-repeat: no-repeat;
                    pointer-events: none;
                    z-index: 10;

                    @media screen and (max-width: 800px) {
                        width: 3.8rem;
                        height: 3.8rem;
                    }

                    @media screen and (max-width: 600px) {
                        width: 3.6rem;
                        height: 3.6rem;
                    }

                    @media screen and (max-width: 460px) {
                        width: 3.2rem;
                        height: 3.2rem;
                    }

                    @media screen and (max-width: 360px) {
                        width: 2.8rem;
                        height: 2.8rem;
                    }
                }
            }

            &-img {
                height: 100%;
                padding-top: calc(100% * 354 / 487);
                position: relative;
                overflow: hidden;
                border-radius: var(--radius);
                width: 100%;

                &::before {
                    display: none;
                }

                img {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    object-fit: cover;
                }
            }
        }

        &-bottom {
            background: $color-white;
            padding: 1.6rem;
            border-radius: var(--radius);

            @media screen and (max-width: 1200px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.8rem;
            }

            &-wrap {
                &.d-wrap {
                    margin-left: -1rem;
                    margin-right: -1rem;

                    @media screen and (max-width: 1200px) {
                        margin-left: -0.5rem;
                        margin-right: -0.5rem;
                    }

                    @media screen and (max-width: 460px) {
                        margin-left: -0.25rem;
                        margin-right: -0.25rem;
                    }
                }

                .swiper {
                    padding: 0.2rem 0;
                }

                .swiper-slide {
                    height: auto;
                    width: calc(100% / 5);

                    @media screen and (max-width: 800px) {
                        width: calc(100% / 6);
                    }

                    &.d-item {
                        padding-left: 1rem;
                        padding-right: 1rem;

                        @media screen and (max-width: 1200px) {
                            padding-left: 0.5rem;
                            padding-right: 0.5rem;
                        }

                        @media screen and (max-width: 460px) {
                            padding-left: 0.25rem;
                            padding-right: 0.25rem;
                        }
                    }
                }

                .swiper-slide-thumb-active {
                    .sp_ct--lf-bottom-item {
                        border-color: $color-text;
                    }
                }
            }

            &-item {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                padding: 0.5rem;
                border-radius: var(--radius);
                border: 0.1rem solid #e2e8f0;
                transition: 0.3s linear;

                @media screen and (max-width: 800px) {
                    padding: 1rem;
                }
            }

            &-img {
                padding-top: 100%;
                width: 100%;
                position: relative;
                overflow: hidden;
                background: $color-white;

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        @media screen and (max-width: 1200px) {
            width: 55%;
            order: 1;
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }
    }

    &--ct {
        width: calc(100% * 4 / 12);

        @media screen and (max-width: 1200px) {
            width: 45%;
            order: 2;
        }

        @media screen and (max-width: 800px) {
            width: 100%;
            order: 2;
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);

            @media screen and (max-width: 1200px) {
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--columnContent2);
            }

           
        }

        &-top {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            @media screen and (max-width: 1200px) {
                // width: calc(55% - (var(--columnContent2) / 2));
                width: 100%;
            }

            @media screen and (max-width: 800px) {
                // width: calc(50% - (var(--columnContent2) / 2));
                width: 100%;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }

        &-qp {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            &-wrap{
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                row-gap: var(--rowContent1);
                column-gap: var(--columnContent1);
            }
        }

        &-price {
            display: flex;
            align-items: center;
            column-gap: 1rem;
            flex-wrap: wrap;
            row-gap: var(--rowContent0);
            .price-normal {
                color: $color-red;
                @include fz-36;
                font-weight: 700;
                white-space: nowrap;
            }

            .price-line {
                font-weight: 600;
                color: $color-text;
                text-decoration: line-through;
                position: relative;
                top: -0.1rem;
                white-space: nowrap;
                @include fz-16;
                // @media screen and (max-width: 1200px) {
                //     top: -0.5rem;
                // }

                // @media screen and (max-width: 600px) {
                //     top: -0.2rem;
                // }
            }
        }

        &-title {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            @media screen and (max-width: 1200px) {
                row-gap: var(--rowContent1);
            }

            &-top {
            }

            &-bottom {
                display: flex;
                // align-items: center;
                column-gap: var(--columnContent2);
                flex-wrap: wrap;
                row-gap: var(--rowContent2);
                &-it {
                    display: flex;
                    // align-items: end;
                    column-gap: 1rem;

                    // &:not(:first-child) {
                    //     position: relative;

                    //     &::before {
                    //         content: "|";
                    //         left: -1.8rem;
                    //         top: 50%;
                    //         transform: translateY(-50%);
                    //         position: absolute;
                    //         color: #94a3b8;
                    //     }
                    // }

                    .note-sm {
                        line-height: 100%;
                        position: relative;
                        top: 0.25rem;

                        @media screen and (max-width: 600px) {
                            top: 0;
                        }
                    }
                }
            }
        }

        &-quantity {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
        }

        &-center {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);

            @media screen and (max-width: 1200px) {
                // width: calc(45% - (var(--columnContent2) / 2));
                width: 100%;
            }

            @media screen and (max-width: 800px) {
                // width: calc(50% - (var(--columnContent2) / 2));
                width: 100%;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }

        &-bottom {
            @media screen and (max-width: 1200px) {
                width: 100%;
            }
        }

        &-btn {
            display: flex;
            column-gap: 1rem;
            animation: anHienTop2 1s linear;
            &.actived {
                position: fixed;
                width: 100%;
                left: 0;
                bottom: 0;
                animation: anHienTop 1s linear;
                column-gap: 0;
                z-index: 1000;

                .btn {
                    border-radius: 0 !important;

                    @media screen and (max-width:600px) {
                        width: 50% !important;

                       
                    }
                }

                .btn.m-buy-now {
                   
                    width: 75% !important;
                    flex-shrink: 0;

                    border-radius: 0 !important;

                    @media screen and (max-width:600px) {
                        width: 50% !important;

                       
                    }
                }
               

                button {
                    border-radius: 0 !important;
                    border-color: $color-white !important;
                    // background: $color-white !important;
                    flex: 1;
                    width: 100%;
                    @media screen and (max-width:600px) {
                        width: 50% !important;

                        i{
                            font-size: 2rem !important;
                        }
                    }
                }

                .btn-add-cart {
                    border-radius: 0 !important;
                    border-color: $color-white !important;
                    background: $color-white !important;

                    @media screen and (max-width:600px) {
                        width: 50% !important;

                        i{
                            font-size: 2rem !important;
                        }
                    }
                }
            }

            .btn {
                &.m-buy-now {
                    height: 5rem;
                    width: 75% !important;
                    flex-shrink: 0;
                }
                height: 5rem;
                width: 75% !important;
                flex-shrink: 0;
                &-text {
                    font-weight: 700;
                    @include fz-24;
                }
                border-radius: var(--radius) !important;
                background: $color-pri !important;
            }

            .btn-add-cart {
                width: calc(100% - 1rem) !important;
                // flex: 1;

                border-radius: var(--radius) !important;
            }

            .single_add_to_cart_button {
                width: calc(25% - 1rem) !important;
                // flex: 1;
                border-radius: var(--radius) !important;
            }
        }

        &-info {
            padding: 1.2rem;
            background: $color-white;
            border-radius: var(--radius);
            border: 0.2rem solid #e2e8f0;
            display: flex;
            flex-direction: column;
            row-gap: 1rem;

            @media screen and (max-width: 800px) {
                row-gap: 0.5rem;
                padding: 1rem;
            }

            ul {
                display: flex;
                flex-direction: column;
                row-gap: 0.2rem;

                li {
                    // padding-left: 1.2rem;
                    position: relative;
                    color: $color-third;
                    // &::before {
                    //     content: "-";
                    //     left: 0;
                    //     top: 50%;
                    //     transform: translateY(-50%);
                    //     position: absolute;
                    // }
                }
            }
        }
    }

    &--rt {
        flex: 1;
        width: 100%;

        @media screen and (max-width: 1200px) {
            order: 3;
        }

        @media screen and (max-width: 800px) {
            order: 3;
        }

        &-wrap {
        }

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            @media screen and (max-width: 1200px) {
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--columnContent2);
            }
        }

        &-item {
            width: 100%;
            // @media screen and (max-width: 1200px) {
            //     width: calc(50% - (var(--columnContent2) / 2));
            // }
        }
    }
}

.quantity {
    &-box {
        border-radius: var(--radius);
        border: 0.1rem solid $color-brown2;
        width: fit-content;
        display: flex;
        align-items: center;
        padding: 0.4rem 0.8rem;
        column-gap: 1.2rem;

    }

    &-btn {
        width: 1.4rem;
        height: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        i {
            @include fz-16;
            line-height: 100%;
            color: $color-brown2;
        }
    }

    &-num {
        height: 100%;
        width: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        .num {
            text-align: center;
            @include fz-16;
            font-weight: 700;
            color: $color-brown2;
        }

        input {
            display: none;
        }
    }
}

.box-des {
    border-radius: var(--radius);
    border: 0.1rem solid $color-pink;
    overflow: hidden;
    &-title {
        padding: 1rem 1.6rem;
        background: $color-pink;

        .note-lg {
            position: relative;
            padding-left: 4rem;

            &::before {
                content: "";
                width: 3.2rem;
                height: 3.2rem;
                position: absolute;
                background-image: url(../assets/images/Ticker.svg);
                background-size: 100% 100%;
                top: 0;
                left: 0;

                @media screen and (max-width: 1200px) {
                    top: -0.2rem;
                }

                @media screen and (max-width: 800px) {
                    width: 3rem;
                    height: 3rem;
                }
            }
        }

        @media screen and (max-width: 1200px) {
            padding: 1rem 1.2rem;
        }
    }

    &-content {
        background: $color-white;
        padding: 1.6rem;
        @media screen and (max-width: 1200px) {
            padding: 1.2rem;
        }
    }

    &-list {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &-item {
        display: flex;
        column-gap: 1.2rem;

        .num {
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            background: $color-brown2;
            color: $color-white;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            @include fz-14;
        }
    }
    ul {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &.ck {
        li {
            padding-left: 3rem;
            position: relative;
            color: $color-third;
            font-weight: 600;
            @include fz-16;

            &::before {
                content: "";
                width: 2rem;
                height: 2rem;
                background-image: url(../assets/images/ck.svg);
                background-size: 100% 100%;
                overflow: hidden;
                position: absolute;
                top: 0.25rem;
                left: 0;
            }
        }
    }

    &.mh {
        li {
            padding-left: 3rem;
            position: relative;
            color: $color-third;
            font-weight: 600;
            @include fz-16;

            &::before {
                content: "";
                width: 2rem;
                height: 2rem;
                background-image: url(../assets/images/p.svg);
                background-size: 100% 100%;
                overflow: hidden;
                position: absolute;
                top: 0.25rem;
                left: 0;
            }
        }
    }
}

.sp_des {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    border-bottom: 0.1rem solid $color-gray9;
    &--form {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &-content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            .btn {
                margin: 0 auto;
            }
            .form-item {
                width: 50%;

                &:last-child {
                    width: 100%;
                }

                @media screen and (max-width: 460px) {
                    width: 100% !important;
                }
            }
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &--top {
        background: #e6e4e4;
        padding: 2rem 0;
        transition: 1s linear;
        animation: anHienTop2 1s linear;
        @keyframes anHienTop {
            0% {
                transform: translateY(-100%);
            }
            50% {
                transform: translateY(-50%);
            }
            100% {
                transform: translateY(0%);
            }
        }

        @keyframes anHienTop2 {
            0% {
                opacity: 0;
            }
            50% {
                opacity: 0.5;
            }
            100% {
                opacity: 1;
            }
        }

        @media screen and (max-width: 1200px) {
            @keyframes anHienTop {
                0% {
                    transform: translateY(100%);
                }
                50% {
                    transform: translateY(50%);
                }
                100% {
                    transform: translateY(0%);
                }
            }
        }

        @media screen and (max-width:1200px) {
            display: none;
        }

        &.actived {
            position: fixed;
            top: 10.5rem;
            width: 100%;
            left: 0;
            z-index: 900;
            animation: anHienTop 0.5s linear;
            padding: 1rem 0;
            @media screen and (max-width: 1200px) {
                top: unset;
                bottom: 0;
            }
        }

        // &.ani {
        //     &.showed {
        //         position: fixed;
        //         top: 12rem;
        //         width: 100%;
        //         left: 0;
        //     }
        // }

        .container {
            max-width: 143rem;
        }

        &-wrap {
            align-items: center;
            display: flex;
            column-gap: 2rem;

            @media screen and (max-width: 1200px) {
                flex-direction: column;
                row-gap: var(--rowContent0);
                align-items: start;
            }
        }

        &-lf {
            width: fit-content;
            overflow: hidden;
            @media screen and (max-width: 1200px) {
                width: 100%;
            }

            &-wrap {
                // .sp_des--tabs {
                //     @media screen and (max-width: 1200px) {
                //         row-gap: var(--rowContent1);
                //         column-gap: var(--columnContent1);
                //     }
                // }
            }
        }

        &-rt {
            // flex-shrink: 0;
            // flex: 1;
            // width: calc(100% - 2rem);
            width: fit-content;
            @media screen and (max-width: 1200px) {
                width: 100%;
            }

            &-wrap {
                display: flex;
                align-items: center;
                column-gap: var(--columnContent2);
                row-gap: var(--rowContent1);

                .sp_ct--ct-price {
                    flex-wrap: nowrap;
                }

                .cart {
                    padding: 0 !important;

                    .btn {
                        background: $color-pri !important;
                        border-radius: var(--radius) !important;
                        @include hover {
                            background: $color-second;
                        }
                    }
                }

                @media screen and (max-width: 1200px) {
                    flex-wrap: wrap;
                }

                @media screen and (max-width: 460px) {
                    flex-direction: column;
                    align-items: start;
                }
                .btn {
                    min-width: 3.2rem;

                    @media screen and (max-width: 460px) {
                        width: 100%;
                    }
                }

                @media screen and (max-width: 1200px) {
                    justify-content: space-between;
                }
            }
        }
    }

    &--tabs {
        // display: flex;
        // align-items: center;
        // column-gap: var(--columnContent2);
        // flex-wrap: wrap;
        // row-gap: var(--rowContent1);

        .swiper-scrollbar {
            @media screen and (max-width: 1200px) {
                margin: 0.5rem 0.8rem 0;
            }
        }

        &.d-wrap {
            margin-left: -1rem;
            margin-right: -1rem;

            @media screen and (max-width: 1200px) {
                margin-left: -0.5rem;
                margin-right: -0.5rem;
            }
        }

        .swiper-slide {
            width: fit-content !important;

            &.d-item {
                padding-left: 1rem;
                padding-right: 1rem;

                @media screen and (max-width: 1200px) {
                    padding-left: 0.5rem;
                    padding-right: 0.5rem;
                }
            }
        }
    }

    &--tab {
        &-link {
            background: $color-white;
            border-radius: var(--radius);
            border: 0.1rem solid $color-text;
            padding: 0.6rem 1.6rem;
            @include fz-18;
            font-weight: 700;
            display: block;
            color: $color-third;
            // white-space: nowrap;
            @include hover {
                background: $color-brown2;
                color: $color-white;
                border-color: $color-brown2;
            }

            @media screen and (max-width: 1200px) {
                padding: 0.4rem 1.4rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.2rem 0.8rem;
                @include fz-14;
            }

            &.actived {
                background: $color-brown2;
                color: $color-white;
                border-color: $color-brown2;
            }
        }
    }

    &--des {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);

        &-content {
            strong {
                color: $color-third;
                font-weight: 600;
                display: inline-block;
            }
        }
    }

    &--bottom {
        &-wrap {
            max-width: 80rem;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent6);
        }
    }

    &--dg {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &-content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        &-filter {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            &-list {
                display: flex;
                align-items: center;
                column-gap: var(--columnContent1);
                row-gap: var(--rowContent1);
                flex-wrap: wrap;
            }

            &-item {
                &.actived {
                    .sp_des--dg-filter-box {
                        border-color: #e5e7eb;
                    }
                }
            }

            &-box {
                display: flex;
                align-items: center;
                column-gap: 0.4rem;
                padding: 0.8rem 1.2rem;
                background: $color-white;
                border-radius: var(--radius);
                border: 0.3rem solid transparent;
                transition: 0.5s linear;
                cursor: pointer;
                white-space: nowrap;

                @media screen and (max-width: 800px) {
                    padding: 0.4rem 1rem;
                }

                @include hover {
                    border-color: #e5e7eb;
                }

                .ic {
                    width: 1.4rem;
                    height: 1.4rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-shrink: 0;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: contain;
                    }
                }
            }
        }
    }
}

.product-dg {
    &-rate {
        display: flex;
        align-items: center;
        column-gap: 0.8rem;
        justify-content: center;
        // margin-bottom: 2.8rem;
        // .star-list {
        //   height: 4rem;
        // }

        &-stars {
            display: flex;
            align-items: center;
