.color {
    &-list {
        display: flex;
        align-items: center;
        column-gap: 1.2rem;
    }

    &-item {
        cursor: pointer;
        input {
            display: none;

            &:checked ~ .box-Color {
                &::before {
                    opacity: 1;
                }

                &::after {
                    opacity: 1;
                }
            }
        }

        .box-Color {
            width: 3.2rem;
            height: 3.2rem;
            flex-shrink: 0;
            border-radius: 50%;
            overflow: hidden;
            display: block;
            box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);
            position: relative;
            &::before {
                content: "";
                position: absolute;
                width: 2.4rem;
                height: 2.4rem;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-image: url(../assets/images/ckcl.svg);
                background-size: 100% 100%;
                background-repeat: no-repeat;
                z-index: 1;
                opacity: 0;
                transition: 0.5s linear;
            }

            &::after {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                width: 100%;
                height: 100%;
                transform: translate(-50%, -50%);
                background: rgba($color: #000000, $alpha: 0.1);
                opacity: 0;
                transition: 0.5s linear;
            }
        }
    }
}

