                @include fz-12;
                padding: 0.3rem 0.3em 0.3rem 2rem;
            }

            &::before {
                content: "";
                position: absolute;
                top: 48%;
                left: 0.6rem;
                transform: translateY(-50%);
                width: 1.2rem;
                height: 1.2rem;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                background-image: url(../assets/images/vm.svg);
            }
        }
