                line-height: 1.5;
            }

            .select2-container--default .select2-selection--single {
                border: none;
                padding: 0.6rem 1.6rem;

                @media screen and (max-width: 600px) {
                    padding: 0.6rem 1.2rem !important;
                }
            }
        }
    }

    &--result {
        &-list {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent2);
            flex-wrap: wrap;
            row-gap: var(--rowContent1);
            max-width: 80%;
            margin: 0 auto;

            @media screen and (max-width: 1200px) {
                max-width: none;
                column-gap: var(--columnContent1);
            }

            @media screen and (max-width: 600px) {
                max-width: none;

                column-gap: var(--columnContent1);
            }
        }

        &-item {
            display: flex;
            align-items: center;
            column-gap: 0.4rem;
            overflow: hidden;
            padding: 0.6rem 1.6rem;

            @media screen and (max-width: 1200px) {
                padding: 0.4rem 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.4rem 1rem;
            }

            cursor: pointer;
            width: fit-content;
            border-radius: var(--radius);
            // padding: 0.6rem 1.6rem;
            background: $color-white;
            border: 0.1rem solid transparent;
            transition: 0.3s linear;

            .ic {
                width: 2rem;
                height: 2rem;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;
                position: relative;
                top: 0.1rem;

                @media screen and (max-width: 800px) {
                    top: 0;
                }
            }

            .note-text {
                color: $color-third;
                white-space: nowrap;

                &:first-child {
                    .ic {
                        display: none;
                    }
                }

                @media screen and (max-width: 360px) {
                    @include fz-12;
                }
            }
        }
    }

    &--list {
        row-gap: var(--rowContent);
    }

    &--content {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);

            .page-numbers {
                margin: 1rem auto 0;

                & > li > .page-numbers {
                    margin-top: 0;
                }
            }
        }
    }

    &--item {
        @media screen and (max-width: 360px) {
            width: 50%;
        }

        @media screen and (max-width: 320px) {
            width: 100%;
        }
    }
}

.pro_des {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        margin: 0 auto;
        max-width: 100.8rem;
        background: $color-white;
        border-radius: var(--radius);
        padding: 4rem;
        position: relative;

        @media screen and (max-width: 1200px) {
            padding: 2.4rem 3rem 3rem 3rem;
        }

        @media screen and (max-width: 800px) {
            padding: 2rem 2rem 3rem 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem 1.2rem 2.4rem 1.2rem;
        }

        .btn {
            position: absolute;
            left: 50%;
            bottom: 0%;
            transform: translate(-50%, 50%);
        }
    }

    &--content {
        overflow: hidden;
        transition: 0.5s linear;
    }

    &--btn {
        .btn {
            &.btn-see-more {
                display: flex;
            }

            &.btn-exit-more {
                display: none;
            }
        }
    }
}
